import React from 'react';
import { VEHICLE_TYPES, VEHICLE_SIZES, FUEL_TYPES } from '../data/engineOilData';
import { INSPECTION_PRICES } from '../data/inspectionData';
import './VehicleInfoModal.css';

const VehicleInfoModal = ({ 
  isOpen, 
  onClose, 
  vehicleInfo, 
  onVehicleInfoChange,
  onConfirm 
}) => {
  if (!isOpen) return null;

  const getInspectionVehicleType = (type, size, fuel) => {
    if (fuel === '') {  // '' '' 
      return {
        id: 'electric',
        name: '()',
        prices: INSPECTION_PRICES.vehicles.find(v => v.id === 'electric')
      };
    }
    
    if (type === '' && size === '') {
      return {
        id: 'compact',
        name: '()',
        prices: INSPECTION_PRICES.vehicles.find(v => v.id === 'compact')
      };
    }
    
    if (type === '' || type === 'SUV') {
      return {
        id: 'small',
        name: '(10 )',
        prices: INSPECTION_PRICES.vehicles.find(v => v.id === 'small')
      };
    }
    
    if (type === '') {
      if (size === '') {
        return {
          id: 'medium',
          name: '(11 /2.5)',
          prices: INSPECTION_PRICES.vehicles.find(v => v.id === 'medium')
        };
      }
      return {
        id: 'small',
        name: '(10 )',
        prices: INSPECTION_PRICES.vehicles.find(v => v.id === 'small')
      };
    }
    
    if (type === '') {
      return {
        id: 'large_truck_10',
        name: ' (10t )',
        prices: INSPECTION_PRICES.vehicles.find(v => v.id === 'large_truck_10')
      };
    }
    
    return {
      id: 'small',
      name: '(10 )',
      prices: INSPECTION_PRICES.vehicles.find(v => v.id === 'small')
    };
  };

  const handleConfirm = () => {
    if (vehicleInfo.type && vehicleInfo.size && vehicleInfo.fuel) {
      const inspectionType = getInspectionVehicleType(
        vehicleInfo.type, 
        vehicleInfo.size, 
        vehicleInfo.fuel
      );
      onConfirm({
        ...vehicleInfo,
        inspectionType: inspectionType
      });
    } else {
      onClose();
    }
  };

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="modal-content vehicle-info-modal" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h2>  </h2>
          <button className="modal-close" onClick={onClose}></button>
        </div>
        <div className="modal-body">
          <div className="vehicle-form">
            <div className="form-section">
              <h4> ()</h4>
              <div className="option-group">
                {VEHICLE_TYPES.map(type => (
                  <button 
                    key={type}
                    className={`option-button ${vehicleInfo.type === type ? 'selected' : ''}`}
                    onClick={() => onVehicleInfoChange('type', type)}
                  >
                    {type}
                  </button>
                ))}
              </div>
            </div>
            
            {vehicleInfo.type && (
              <div className="form-section">
                <h4>  ()</h4>
                <div className="option-group">
                  {VEHICLE_SIZES[vehicleInfo.type]?.map(size => (
                    <button 
                      key={size}
                      className={`option-button ${vehicleInfo.size === size ? 'selected' : ''}`}
                      onClick={() => onVehicleInfoChange('size', size)}
                    >
                      {size}
                    </button>
                  ))}
                </div>
              </div>
            )}
            
            <div className="form-section">
              <h4> ()</h4>
              <div className="option-group">
                {FUEL_TYPES.map(fuel => (
                  <button 
                    key={fuel}
                    className={`option-button ${vehicleInfo.fuel === fuel ? 'selected' : ''}`}
                    onClick={() => onVehicleInfoChange('fuel', fuel)}
                  >
                    {fuel}
                  </button>
                ))}
              </div>
            </div>
            
            {vehicleInfo.type && vehicleInfo.size && vehicleInfo.fuel && (
              <div className="vehicle-type-display">
                <div className="vehicle-type-label">   </div>
                <div className="vehicle-type-value">
                  {getInspectionVehicleType(vehicleInfo.type, vehicleInfo.size, vehicleInfo.fuel).name}
                </div>
                <div className="vehicle-type-note">
                  *      .
                </div>
              </div>
            )}
          </div>
        </div>
        <div className="modal-footer">
          <button 
            className="modal-button primary"
            onClick={handleConfirm}
          >
            
          </button>
        </div>
      </div>
    </div>
  );
};

export default VehicleInfoModal;