// src/pages/c-cont-02/c-cont-02.js
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import './c-cont-02.css';

function CCont02() {
  const navigate = useNavigate();
  
  //   
  const [activeTab, setActiveTab] = useState('all');
  
  //   ( API )
  const [contents, setContents] = useState([
    { id: 1, type: 'posting', title: '   ', date: '2025-07-22' },
    { id: 2, type: 'notice', title: '   ', date: '2025-07-24' },
    { id: 3, type: 'posting', title: '   ', date: '2025-07-10' },
    { id: 4, type: 'notice', title: '   ', date: '2025-05-14' },
    { id: 5, type: 'posting', title: ',   ?', date: '2025-06-25' },
  ]);

  //  
  const filteredContents = contents.filter(content => {
    if (activeTab === 'all') return true;
    return content.type === activeTab;
  });

  //   
  const counts = {
    all: contents.length,
    notice: contents.filter(c => c.type === 'notice').length,
    posting: contents.filter(c => c.type === 'posting').length,
  };

  //  
  const handleDelete = (id) => {
    if (window.confirm(' ?')) {
      setContents(contents.filter(content => content.id !== id));
      alert('.');
    }
  };

  //  
  const handleEdit = (content) => {
    navigate('/c-cont-02-write', { state: { content } });
  };

  //   
  const handleNewPost = () => {
    navigate('/c-cont-02-write');
  };

  return (
    <div className="content-management">
      <div className="content-header">
        <h2>  </h2>
        <button className="new-post-button" onClick={handleNewPost}>
          +   
        </button>
      </div>

      {/*   */}
      <div className="content-tabs">
        <button 
          className={`tab ${activeTab === 'all' ? 'active' : ''}`}
          onClick={() => setActiveTab('all')}
        >
          ({counts.all})
        </button>
        <button 
          className={`tab ${activeTab === 'notice' ? 'active' : ''}`}
          onClick={() => setActiveTab('notice')}
        >
          ({counts.notice})
        </button>
        <button 
          className={`tab ${activeTab === 'posting' ? 'active' : ''}`}
          onClick={() => setActiveTab('posting')}
        >
          ({counts.posting})
        </button>
      </div>

      {/*   */}
      <div className="content-table">
        <table>
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {filteredContents.map(content => (
              <tr key={content.id}>
                <td>
                  <span className={`type-badge ${content.type}`}>
                    {content.type === 'notice' ? '' : ''}
                  </span>
                </td>
                <td className="title-cell">{content.title}</td>
                <td>{content.date}</td>
                <td className="action-cell">
                  <button 
                    className="edit-button"
                    onClick={() => handleEdit(content)}
                  >
                    
                  </button>
                  <button 
                    className="delete-button"
                    onClick={() => handleDelete(content.id)}
                  >
                    
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/*  */}
      <div className="pagination">
        <button className="page-button active">1</button>
      </div>
    </div>
  );
}

export default CCont02;