// src/pages/c-cs-02/c-cs-02.js
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import './c-cs-02.css';

function CCs02() {
  const navigate = useNavigate();
  
  //    
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: '',
    title: '',
    content: ''
  });

  //   
  const handleChange = (e) => {
    const { name, value } = e.target;
    
    //   500 
    if (name === 'content' && value.length > 500) {
      return;
    }
    
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  //   
  const handleSubmit = (e) => {
    e.preventDefault();
    
    //   
    if (!formData.email || !formData.title || !formData.content) {
      alert('   .');
      return;
    }

    //   
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(formData.email)) {
      alert('   .');
      return;
    }

    // mailto  
    const subject = encodeURIComponent(`[  ] ${formData.title}`);
    const body = encodeURIComponent(
      ` : ${formData.name}\n` +
      `: ${formData.email}\n` +
      `: ${formData.phone}\n` +
      `\n--- ---\n\n` +
      `${formData.content}`
    );
    
    const mailtoLink = `mailto:cs@chaba.co.kr?subject=${subject}&body=${body}`;
    
    //   
    window.location.href = mailtoLink;
    
    //  
    setFormData({
      name: '',
      email: '',
      phone: '',
      title: '',
      content: ''
    });
    
    //  
    setTimeout(() => {
      alert(' .    .');
    }, 1000);
  };

  return (
    <div className="cs-container">
      <div className="cs-header">
        <button 
          className="back-button"
          onClick={() => navigate(-1)}
        >
           
        </button>
        <h2>1:1 </h2>
        <div></div>
      </div>

      <form className="cs-form" onSubmit={handleSubmit}>
        <div className="form-section">
          <h3> </h3>
          
          <div className="form-group">
            <label htmlFor="name"></label>
            <input
              type="text"
              id="name"
              name="name"
              value={formData.name}
              onChange={handleChange}
              placeholder=" "
            />
          </div>

          <div className="form-group">
            <label htmlFor="email">
               <span className="required">*</span>
            </label>
            <input
              type="email"
              id="email"
              name="email"
              value={formData.email}
              onChange={handleChange}
              placeholder="example@email.com"
              required
            />
          </div>

          <div className="form-group">
            <label htmlFor="phone"></label>
            <input
              type="tel"
              id="phone"
              name="phone"
              value={formData.phone}
              onChange={handleChange}
              placeholder="010-1234-5678"
            />
          </div>
        </div>

        <div className="form-section">
          <div className="form-group">
            <label htmlFor="title">
               <span className="required">*</span>
            </label>
            <input
              type="text"
              id="title"
              name="title"
              value={formData.title}
              onChange={handleChange}
              placeholder="  "
              required
            />
          </div>

          <div className="form-group">
            <label htmlFor="content">
               <span className="required">*</span>
            </label>
            <textarea
              id="content"
              name="content"
              value={formData.content}
              onChange={handleChange}
              placeholder="  "
              rows="8"
              required
            />
            <div className="char-count">
              {formData.content.length} / 500
            </div>
          </div>
        </div>

        <button type="submit" className="submit-button">
           
        </button>
      </form>
    </div>
  );
}

export default CCs02;