// src/pages/c-join/c-join.js
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import './c-join.css';

function CJoin() {
  const navigate = useNavigate();
  
  //    
  const [formData, setFormData] = useState({
    //  
    partnerType: 'carCenter', // carCenter or inspection
    
    //  
    businessNumber: '',
    password: '',
    passwordConfirm: '',
    
    //    
    companyName: '',
    ceoName: '',
    ceoPhone: '',
    companyPhone: '',
    email: '',
    address: '',
    addressDetail: '',
    
    //  
    bankName: '',
    accountNumber: '',
    
    //  
    agreeAll: false,
    agreeTerms: false,
    agreePrivacy: false,
    agreeLocation: false,
    agreeMarketing: false
  });

  //  
  const formatBusinessNumber = (value) => {
    const numbers = value.replace(/[^\d]/g, '');
    if (numbers.length <= 3) return numbers;
    if (numbers.length <= 5) return `${numbers.slice(0, 3)}-${numbers.slice(3)}`;
    return `${numbers.slice(0, 3)}-${numbers.slice(3, 5)}-${numbers.slice(5, 10)}`;
  };

  //  
  const formatPhoneNumber = (value) => {
    const numbers = value.replace(/[^\d]/g, '');
    if (numbers.length <= 3) return numbers;
    if (numbers.length <= 7) return `${numbers.slice(0, 3)}-${numbers.slice(3)}`;
    return `${numbers.slice(0, 3)}-${numbers.slice(3, 7)}-${numbers.slice(7, 11)}`;
  };

  //  
  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    
    if (name === 'businessNumber') {
      setFormData(prev => ({ ...prev, [name]: formatBusinessNumber(value) }));
    } else if (name === 'ceoPhone' || name === 'companyPhone') {
      setFormData(prev => ({ ...prev, [name]: formatPhoneNumber(value) }));
    } else if (type === 'checkbox') {
      setFormData(prev => ({ ...prev, [name]: checked }));
    } else {
      setFormData(prev => ({ ...prev, [name]: value }));
    }
  };

  //   
  const handleAgreeAll = (checked) => {
    setFormData(prev => ({
      ...prev,
      agreeAll: checked,
      agreeTerms: checked,
      agreePrivacy: checked,
      agreeLocation: checked,
      agreeMarketing: checked
    }));
  };

  //   (  API  )
  const handleAddressSearch = () => {
    alert('     .');
  };

  //  
  const handleBusinessVerify = () => {
    alert('     .');
  };

  //  
  const handleAccountVerify = () => {
    alert('     .');
  };

  //  
  const handleSubmit = (e) => {
    e.preventDefault();
    
    //  
    if (formData.password !== formData.passwordConfirm) {
      alert('  .');
      return;
    }
    
    //    
    if (!formData.agreeTerms || !formData.agreePrivacy) {
      alert('  .');
      return;
    }
    
    console.log(' :', formData);
    
    //   
    alert('  .\n\n   ,   1~2 \n   .');
    
    //   
    navigate('/c-log');
  };

  return (
    <div className="c-join-wrapper">
      <div className="c-join-container">
        <div className="c-join-header">
          <h1 onClick={() => navigate('/c-log')}> </h1>
          <h2></h2>
        </div>

        <form className="c-join-form" onSubmit={handleSubmit}>
          {/*    */}
          <div className="form-section">
            <h3> </h3>
            <select 
              name="partnerType" 
              value={formData.partnerType}
              onChange={handleChange}
              className="form-select"
            >
              <option value="carCenter"></option>
              <option value="inspection"></option>
            </select>
          </div>

          {/*   */}
          <div className="form-section">
            <h3> </h3>
            
            <div className="form-group with-button">
              <label></label>
              <div className="input-with-button">
                <input
                  type="text"
                  name="businessNumber"
                  value={formData.businessNumber}
                  onChange={handleChange}
                  placeholder="123-45-67890"
                  maxLength="12"
                  required
                />
                <button type="button" onClick={handleBusinessVerify} className="btn-verify">
                  
                </button>
              </div>
            </div>

            <div className="form-group">
              <label></label>
              <input
                type="password"
                name="password"
                value={formData.password}
                onChange={handleChange}
                placeholder=" "
                required
              />
            </div>

            <div className="form-group">
              <label> </label>
              <input
                type="password"
                name="passwordConfirm"
                value={formData.passwordConfirm}
                onChange={handleChange}
                placeholder="  "
                required
              />
            </div>
          </div>

          {/*     */}
          <div className="form-section">
            <h3>   </h3>
            
            <div className="form-group">
              <label></label>
              <input
                type="text"
                name="companyName"
                value={formData.companyName}
                onChange={handleChange}
                placeholder="()"
                required
              />
            </div>

            <div className="form-group">
              <label></label>
              <input
                type="text"
                name="ceoName"
                value={formData.ceoName}
                onChange={handleChange}
                placeholder=""
                required
              />
            </div>

            <div className="form-group">
              <label> </label>
              <input
                type="tel"
                name="ceoPhone"
                value={formData.ceoPhone}
                onChange={handleChange}
                placeholder="010-1234-5678"
                maxLength="13"
                required
              />
            </div>

            <div className="form-group">
              <label> </label>
              <input
                type="tel"
                name="companyPhone"
                value={formData.companyPhone}
                onChange={handleChange}
                placeholder="02-1234-5678"
                maxLength="13"
                required
              />
            </div>

            <div className="form-group">
              <label></label>
              <input
                type="email"
                name="email"
                value={formData.email}
                onChange={handleChange}
                placeholder="ceo@chabamotor.com"
                required
              />
            </div>

            <div className="form-group with-button">
              <label></label>
              <div className="input-with-button">
                <input
                  type="text"
                  name="address"
                  value={formData.address}
                  onChange={handleChange}
                  placeholder=" "
                  readOnly
                  required
                />
                <button type="button" onClick={handleAddressSearch} className="btn-search">
                  
                </button>
              </div>
              <input
                type="text"
                name="addressDetail"
                value={formData.addressDetail}
                onChange={handleChange}
                placeholder=" "
                className="mt-8"
              />
            </div>
          </div>

          {/*   */}
          <div className="form-section">
            <h3> </h3>
            
            <div className="form-group">
              <label></label>
              <select 
                name="bankName" 
                value={formData.bankName}
                onChange={handleChange}
                className="form-select"
                required
              >
                <option value=""> </option>
                <option value="kb"></option>
                <option value="shinhan"></option>
                <option value="woori"></option>
                <option value="hana"></option>
                <option value="nh"></option>
              </select>
            </div>

            <div className="form-group with-button">
              <label></label>
              <div className="input-with-button">
                <input
                  type="text"
                  name="accountNumber"
                  value={formData.accountNumber}
                  onChange={handleChange}
                  placeholder="123-456-789012"
                  required
                />
                <button type="button" onClick={handleAccountVerify} className="btn-verify">
                  
                </button>
              </div>
            </div>
          </div>

          {/*   */}
          <div className="form-section">
            <h3> </h3>
            
            <div className="form-group">
              <label></label>
              <input type="file" accept=".pdf,.jpg,.jpeg,.png" />
            </div>

            <div className="form-group">
              <label>/</label>
              <input type="file" accept=".pdf,.jpg,.jpeg,.png" />
            </div>
          </div>

          {/*   */}
          <div className="form-section">
            <h3> </h3>
            
            <div className="agree-all">
              <label>
                <input
                  type="checkbox"
                  checked={formData.agreeAll}
                  onChange={(e) => handleAgreeAll(e.target.checked)}
                />
                <span> </span>
              </label>
            </div>

            <div className="agree-list">
              <label>
                <input
                  type="checkbox"
                  name="agreeTerms"
                  checked={formData.agreeTerms}
                  onChange={handleChange}
                />
                <span>[] </span>
                <button type="button" className="btn-link" onClick={() => alert(' ')}>
                  
                </button>
              </label>

              <label>
                <input
                  type="checkbox"
                  name="agreePrivacy"
                  checked={formData.agreePrivacy}
                  onChange={handleChange}
                />
                <span>[]    </span>
                <button type="button" className="btn-link" onClick={() => alert('    ')}>
                  
                </button>
              </label>

              <label>
                <input
                  type="checkbox"
                  name="agreeLocation"
                  checked={formData.agreeLocation}
                  onChange={handleChange}
                />
                <span>[]   </span>
                <button type="button" className="btn-link" onClick={() => alert('   ')}>
                  
                </button>
              </label>

              <label>
                <input
                  type="checkbox"
                  name="agreeMarketing"
                  checked={formData.agreeMarketing}
                  onChange={handleChange}
                />
                <span>[]   </span>
                <button type="button" className="btn-link" onClick={() => alert('   ')}>
                  
                </button>
              </label>
            </div>
          </div>

          {/*  */}
          <div className="form-buttons">
            <button type="button" onClick={() => navigate('/c-log')} className="btn-cancel">
              
            </button>
            <button type="submit" className="btn-submit">
              
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}

export default CJoin;