import React from 'react';

function SaveConfirmModal({ isOpen, onCancel, onConfirm, onSkip }) {
  if (!isOpen) return null;

  return (
    <div className="save-modal-overlay">
      <div className="save-modal-content">
        <div className="save-modal-body">
          <p>   .</p>
          <p>?</p>
        </div>
        <div className="save-modal-footer">
          <button className="btn-cancel" onClick={onCancel}></button>
          <button className="btn-skip" onClick={onSkip}>  </button>
          <button className="btn-save" onClick={onConfirm}>  </button>
        </div>
      </div>
    </div>
  );
}

export default SaveConfirmModal;