// src/pages/c-store-02/c-store-02.js
import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import './c-store-02.css';
import DiscountPolicy from './DiscountPolicy';

//   import
import { ENGINE_OIL_DATA, VEHICLE_TYPES, VEHICLE_SIZES, FUEL_TYPES } from '../../data/engineOilData';
import { INSPECTION_PRICES } from '../../data/inspectionData';

function CStore02() {
  const [partnerType, setPartnerType] = useState(
    localStorage.getItem('partnerType') || 'carCenter'
  );
  const [hasUnsavedChanges, setHasUnsavedChanges] = useState(false);
  
  //  
  const [inspectionData, setInspectionData] = useState({
    prices: INSPECTION_PRICES.vehicles.map(v => ({
      ...v,
      enabled: v.regular > 0 || v.comprehensive_load > 0
    })),
    noxEnabled: true,
    noxPrice: 15000,
    capacity: 5
  });
  
  //  
  const [selectedService, setSelectedService] = useState('inspection');
  const [selectedOilType, setSelectedOilType] = useState('eco'); // eco or standard
  const [selectedVehicleType, setSelectedVehicleType] = useState('');
  const [carCenterServices, setCarCenterServices] = useState({
    inspection: { enabled: true, available: true },
    airFilter: { enabled: true }
  });
  
  //   
  const [engineOilSettings, setEngineOilSettings] = useState(() => {
    //    -  /   enabled  
    const settings = { eco: {}, standard: {} };
    
    ['eco', 'standard'].forEach(oilType => {
      VEHICLE_TYPES.forEach(vehicleType => {
        settings[oilType][vehicleType] = {};
        settings[oilType][vehicleType].enabled = true; //   On/Off
        settings[oilType][vehicleType].sizes = {};
        
        const sizes = VEHICLE_SIZES[vehicleType] || [];
        sizes.forEach(size => {
          settings[oilType][vehicleType].sizes[size] = {};
          FUEL_TYPES.forEach(fuel => {
            const data = ENGINE_OIL_DATA[oilType]?.[vehicleType]?.[size]?.[fuel];
            settings[oilType][vehicleType].sizes[size][fuel] = {
              enabled: data !== null,
              available: data !== null
            };
          });
        });
      });
    });
    
    return settings;
  });

  //    
  useEffect(() => {
    const handleStorageChange = () => {
      const newType = localStorage.getItem('partnerType') || 'carCenter';
      setPartnerType(newType);
    };

    window.addEventListener('storage', handleStorageChange);
    window.addEventListener('partnerTypeChanged', handleStorageChange);

    return () => {
      window.removeEventListener('storage', handleStorageChange);
      window.removeEventListener('partnerTypeChanged', handleStorageChange);
    };
  }, []);

  //  
  const handleInspectionPriceChange = (index, field, value) => {
    const newPrices = [...inspectionData.prices];
    newPrices[index][field] = parseInt(value) || 0;
    setInspectionData({ ...inspectionData, prices: newPrices });
    setHasUnsavedChanges(true);
  };

  const handleVehicleToggle = (index) => {
    const newPrices = [...inspectionData.prices];
    newPrices[index].enabled = !newPrices[index].enabled;
    setInspectionData({ ...inspectionData, prices: newPrices });
    setHasUnsavedChanges(true);
  };

  const handleNoxToggle = () => {
    setInspectionData({ ...inspectionData, noxEnabled: !inspectionData.noxEnabled });
    setHasUnsavedChanges(true);
  };

  const handleNoxPriceChange = (value) => {
    setInspectionData({ ...inspectionData, noxPrice: parseInt(value) || 0 });
    setHasUnsavedChanges(true);
  };

  const handleCapacityChange = (value) => {
    setInspectionData({ ...inspectionData, capacity: parseInt(value) || 0 });
    setHasUnsavedChanges(true);
  };

  //  
  const handleCarCenterServiceToggle = (service) => {
    const newServices = { ...carCenterServices };
    newServices[service].enabled = !newServices[service].enabled;
    setCarCenterServices(newServices);
    setHasUnsavedChanges(true);
  };

  //   
  const handleEngineOilToggle = (vehicleType, size = null, fuel = null) => {
    const newSettings = JSON.parse(JSON.stringify(engineOilSettings)); // Deep copy
    
    if (!size && !fuel) {
      //   
      const currentEnabled = newSettings[selectedOilType][vehicleType].enabled;
      newSettings[selectedOilType][vehicleType].enabled = !currentEnabled;
      
      //  /      
      if (!currentEnabled) {
        //       
        const sizes = VEHICLE_SIZES[vehicleType] || [];
        sizes.forEach(size => {
          FUEL_TYPES.forEach(fuel => {
            if (newSettings[selectedOilType][vehicleType].sizes[size][fuel].available) {
              newSettings[selectedOilType][vehicleType].sizes[size][fuel].enabled = true;
            }
          });
        });
      } else {
        //      
        const sizes = VEHICLE_SIZES[vehicleType] || [];
        sizes.forEach(size => {
          FUEL_TYPES.forEach(fuel => {
            newSettings[selectedOilType][vehicleType].sizes[size][fuel].enabled = false;
          });
        });
      }
    } else if (size && fuel) {
      //   
      const current = newSettings[selectedOilType][vehicleType].sizes[size][fuel];
      if (current.available) {
        current.enabled = !current.enabled;
      }
    }
    
    setEngineOilSettings(newSettings);
    setHasUnsavedChanges(true);
  };

  // 
  const handleSave = () => {
    const saveData = partnerType === 'inspection' 
      ? { type: 'inspection', data: inspectionData }
      : { 
          type: 'carCenter', 
          services: carCenterServices,
          engineOilSettings: engineOilSettings
        };
    
    console.log(' :', saveData);
    alert(' .');
    setHasUnsavedChanges(false);
  };

  //  UI
  const renderInspectionUI = () => (
    <div className="inspection-content">
      <div className="section">
        <h2 className="section-title"></h2>
        <p className="section-description">
          *    ,      0  .
        </p>
        
        <table className="price-table">
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th>()</th>
              <th>()</th>
              <th>()</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {inspectionData.prices.map((vehicle, index) => (
              <tr key={vehicle.id}>
                <td>{vehicle.name}</td>
                <td>
                  <input
                    type="number"
                    value={vehicle.regular}
                    onChange={(e) => handleInspectionPriceChange(index, 'regular', e.target.value)}
                  />
                </td>
                <td>
                  <input
                    type="number"
                    value={vehicle.comprehensive_load}
                    onChange={(e) => handleInspectionPriceChange(index, 'comprehensive_load', e.target.value)}
                  />
                </td>
                <td>
                  <input
                    type="number"
                    value={vehicle.comprehensive_no_load}
                    onChange={(e) => handleInspectionPriceChange(index, 'comprehensive_no_load', e.target.value)}
                  />
                </td>
                <td>
                  <input
                    type="number"
                    value={vehicle.comprehensive_exempt}
                    onChange={(e) => handleInspectionPriceChange(index, 'comprehensive_exempt', e.target.value)}
                  />
                </td>
                <td>
                  <input
                    type="checkbox"
                    checked={vehicle.enabled}
                    onChange={() => handleVehicleToggle(index)}
                  />
                </td>
              </tr>
            ))}
          </tbody>
        </table>
        <div className="price-unit">(: )</div>
      </div>

      <hr className="section-divider" />

      <div className="section">
        <h2 className="section-title">   </h2>
        
        <div className="additional-items">
          <div className="additional-item">
            <div className="additional-item-name">
              <span></span>
            </div>
            <span></span>
            <span> ()</span>
          </div>
          
          <div className="additional-item">
            <div className="additional-item-name">
              NOx 
              <span className="help-icon" title="(NOx)   .">(?)</span>
            </div>
            <input
              type="checkbox"
              checked={inspectionData.noxEnabled}
              onChange={handleNoxToggle}
            />
            <input
              type="number"
              value={inspectionData.noxPrice}
              onChange={(e) => handleNoxPriceChange(e.target.value)}
              disabled={!inspectionData.noxEnabled}
            />
          </div>
        </div>
      </div>

      <hr className="section-divider" />

      <div className="section">
        <div className="capacity-setting">
          <label>
               
            <span className="help-icon" title="     .">(?)</span>
          </label>
          <input
            type="number"
            className="capacity-input"
            value={inspectionData.capacity}
            onChange={(e) => handleCapacityChange(e.target.value)}
            min="0"
            max="99"
          />
          <span className="capacity-unit"> / </span>
        </div>
      </div>
    </div>
  );

  //  UI
  const renderCarCenterUI = () => (
    <div className="carcenter-content">
      <div className="service-sidebar">
        <button
          className={`service-menu-item ${selectedService === 'discount' ? 'active' : ''}`}
          onClick={() => setSelectedService('discount')}
        >
            
        </button>
        
        <button
          className={`service-menu-item ${selectedService === 'inspection' ? 'active' : ''}`}
          onClick={() => setSelectedService('inspection')}
        >
            
        </button>
        
        <button
          className={`service-menu-item ${selectedService === 'engineOil' ? 'active' : ''}`}
          onClick={() => setSelectedService('engineOil')}
        >
          
        </button>
        {selectedService === 'engineOil' && (
          <div className="service-submenu">
            <button
              className={`service-submenu-item ${selectedOilType === 'eco' ? 'active' : ''}`}
              onClick={() => setSelectedOilType('eco')}
            >
              ()
            </button>
            <button
              className={`service-submenu-item ${selectedOilType === 'standard' ? 'active' : ''}`}
              onClick={() => setSelectedOilType('standard')}
            >
              ()
            </button>
          </div>
        )}
        
        <button
          className={`service-menu-item ${selectedService === 'airFilter' ? 'active' : ''}`}
          onClick={() => setSelectedService('airFilter')}
        >
           
        </button>
      </div>

      <div className="service-main-content">
        {selectedService === 'inspection' && (
          <>
            <h2 className="section-title">    </h2>
            <p className="section-description">
              *  '  '   .
            </p>
            
            <div className="service-toggle">
              <div className="service-info">
                <span className="service-name">  </span>
                <span className="service-price">(35,000)</span>
              </div>
              
              <div className="toggle-switch">
                <input
                  type="radio"
                  id="inspection-on"
                  name="inspection"
                  checked={carCenterServices.inspection.enabled}
                  onChange={() => handleCarCenterServiceToggle('inspection')}
                  disabled={!carCenterServices.inspection.available}
                />
                <label htmlFor="inspection-on" className="toggle-label">On</label>
                
                <input
                  type="radio"
                  id="inspection-off"
                  name="inspection"
                  checked={!carCenterServices.inspection.enabled}
                  onChange={() => handleCarCenterServiceToggle('inspection')}
                  disabled={!carCenterServices.inspection.available}
                />
                <label htmlFor="inspection-off" className="toggle-label">Off</label>
              </div>
            </div>
            
            {!carCenterServices.inspection.available && (
              <div className="service-unavailable">
                <p>
                           .<br />
                  <Link to="/c-cs-02">1:1 </Link>   .    .
                </p>
              </div>
            )}
          </>
        )}
        
        {selectedService === 'discount' && (
        <DiscountPolicy onSave={(policies) => {
            console.log('  :', policies);
            setHasUnsavedChanges(true); //   
        }} />
        )}
        
        {selectedService === 'engineOil' && (
          <div className="engine-oil-container">
            <h2 className="section-title">  </h2>
            <p className="section-description">
              *        .
            </p>
            
            {/*    */}
            <div className="oil-type-nav">
              <button
                className={`oil-type-nav-item ${selectedOilType === 'eco' ? 'active' : ''}`}
                onClick={() => setSelectedOilType('eco')}
              >
                ()
              </button>
              <button
                className={`oil-type-nav-item ${selectedOilType === 'standard' ? 'active' : ''}`}
                onClick={() => setSelectedOilType('standard')}
              >
                ()
              </button>
            </div>
            
            {/*    */}
            <div className="vehicle-type-selector">
              {VEHICLE_TYPES.map(vehicleType => (
                <button
                  key={vehicleType}
                  className={`vehicle-type-btn ${selectedVehicleType === vehicleType ? 'active' : ''}`}
                  onClick={() => setSelectedVehicleType(vehicleType)}
                >
                  <span className="vehicle-type-icon">
                    {vehicleType === '' && ''}
                    {vehicleType === 'SUV' && ''}
                    {vehicleType === '' && ''}
                    {vehicleType === '' && ''}
                  </span>
                  <span>{vehicleType}</span>
                </button>
              ))}
            </div>
            
            {/*     */}
            {selectedVehicleType && (
              <>
                {/*  On/Off */}
                <div className="master-control">
                  <span className="master-control-label">
                    {selectedVehicleType} -  {selectedOilType === 'eco' ? '()' : '()'} 
                  </span>
                  <button
                    className={`select-all-btn ${engineOilSettings[selectedOilType][selectedVehicleType].enabled ? 'active' : ''}`}
                    onClick={() => handleEngineOilToggle(selectedVehicleType)}
                  >
                    {engineOilSettings[selectedOilType][selectedVehicleType].enabled ? '  ' : ' '}
                  </button>
                </div>
                
                {/*   */}
                <table className="engine-oil-table">
                  <thead>
                    <tr>
                      <th> </th>
                      {FUEL_TYPES.map(fuel => (
                        <th key={fuel}>
                          {fuel}
                          <br />
                          <span style={{ fontWeight: 'normal', fontSize: '14px', color: '#6c757d' }}>
                            (On/Off)
                          </span>
                        </th>
                      ))}
                    </tr>
                  </thead>
                  <tbody>
                    {VEHICLE_SIZES[selectedVehicleType].map(size => (
                      <tr key={size}>
                        <td>{size}</td>
                        {FUEL_TYPES.map(fuel => {
                          const data = ENGINE_OIL_DATA[selectedOilType]?.[selectedVehicleType]?.[size]?.[fuel];
                          const setting = engineOilSettings[selectedOilType][selectedVehicleType].sizes[size][fuel];
                          
                          return (
                            <td key={fuel}>
                              {data ? (
                                <div
                                  className={`price-cell ${setting.enabled ? 'selected' : ''} ${
                                    setting.enabled && data.viscosity ? `viscosity-${data.viscosity.toLowerCase()}` : ''
                                  }`}
                                  onClick={() => handleEngineOilToggle(selectedVehicleType, size, fuel)}
                                  role="button"
                                  tabIndex={0}
                                  onKeyPress={(e) => {
                                    if (e.key === 'Enter' || e.key === ' ') {
                                      handleEngineOilToggle(selectedVehicleType, size, fuel);
                                    }
                                  }}
                                >
                                  <div className="price-amount">
                                    {data.price.toLocaleString()}
                                  </div>
                                  {data.viscosity && (
                                    <div className="viscosity-info">
                                      {data.viscosity}
                                    </div>
                                  )}
                                </div>
                              ) : (
                                <span className="not-available-cell">-</span>
                              )}
                            </td>
                          );
                        })}
                      </tr>
                    ))}
                  </tbody>
                </table>
                
                {/*   */}
                <div className="viscosity-legend">
                  <div className="legend-item">
                    <div className="legend-color viscosity-0w20"></div>
                    <span>0W20 </span>
                  </div>
                  <div className="legend-item">
                    <div className="legend-color viscosity-5w30"></div>
                    <span>5W30 </span>
                  </div>
                </div>
              </>
            )}
            
            {/*    */}
            {!selectedVehicleType && (
              <div className="empty-state">
                <div className="empty-state-icon"></div>
                <div className="empty-state-text">
                   .
                </div>
              </div>
            )}
          </div>
        )}
        
        {selectedService === 'airFilter' && (
          <>
            <h2 className="section-title">   </h2>
            <p className="section-description">
              *  ' '   .
            </p>
            
            <div className="service-toggle">
              <div className="service-info">
                <span className="service-name">  </span>
                <span className="service-price">(25,000)</span>
              </div>
              
              <div className="toggle-switch">
                <input
                  type="radio"
                  id="airfilter-on"
                  name="airfilter"
                  checked={carCenterServices.airFilter.enabled}
                  onChange={() => handleCarCenterServiceToggle('airFilter')}
                />
                <label htmlFor="airfilter-on" className="toggle-label">On</label>
                
                <input
                  type="radio"
                  id="airfilter-off"
                  name="airfilter"
                  checked={!carCenterServices.airFilter.enabled}
                  onChange={() => handleCarCenterServiceToggle('airFilter')}
                />
                <label htmlFor="airfilter-off" className="toggle-label">Off</label>
              </div>
            </div>
          </>
        )}
      </div>
    </div>
  );

  return (
    <div className="c-store-02">
      <div className="page-header">
        <h1> </h1>
        <button 
          className="btn-save"
          onClick={handleSave}
          disabled={!hasUnsavedChanges}
        >
          
        </button>
      </div>
      
      {partnerType === 'inspection' && (
        <p className="page-description">
          *        .
        </p>
      )}

      {partnerType === 'inspection' ? renderInspectionUI() : renderCarCenterUI()}
    </div>
  );
}

export default CStore02;