import React, { useState, useMemo } from 'react';
import DateModal from '../../components/DateModal/DateModal';
import './c-res-01-01.css';

function CRes0101() {
  const [selectedDateType, setSelectedDateType] = useState('inspection');
  const [searchType, setSearchType] = useState('all');
  const [searchKeyword, setSearchKeyword] = useState('');
  const [startDate, setStartDate] = useState({ year: 2025, month: 7, day: 1 });
  const [endDate, setEndDate] = useState({ year: 2025, month: 7, day: 31 });
  const [showDateModal, setShowDateModal] = useState(false);
  const [dateModalType, setDateModalType] = useState('start');
  const [showDetailModal, setShowDetailModal] = useState(false);
  const [selectedReservation, setSelectedReservation] = useState(null);
  
  //   
  const [sortConfig, setSortConfig] = useState({ key: null, direction: 'asc' });

  //    
  const [reservations, setReservations] = useState([
    {
      id: 1,
      status: '',
      inspectionDate: '2025-07-02',
      displayDate: '07.02()',
      time: ' 9:00',
      vehicleNumber: '231224',
      phone: '010-7303-3168',
      vehicleType: '',
      inspectionType: '',
      visitStatus: ' '
    },
    {
      id: 2,
      status: '',
      inspectionDate: '2025-07-02',
      displayDate: '07.02()',
      time: ' 10:00',
      vehicleNumber: '326936',
      phone: '010-4952-7782',
      vehicleType: '',
      inspectionType: '',
      visitStatus: ' '
    },
    {
      id: 3,
      status: '',
      inspectionDate: '2025-07-02',
      displayDate: '07.02()',
      time: ' 11:00',
      vehicleNumber: '158293',
      phone: '010-6527-2889',
      vehicleType: '',
      inspectionType: '',
      visitStatus: ' '
    },
    {
      id: 4,
      status: '',
      inspectionDate: '2025-07-02',
      displayDate: '07.02()',
      time: ' 11:00',
      vehicleNumber: '202833',
      phone: '010-4184-0842',
      vehicleType: '',
      inspectionType: '',
      visitStatus: ''
    },
    {
      id: 5,
      status: '',
      inspectionDate: '2025-07-02',
      displayDate: '07.02()',
      time: ' 3:00',
      vehicleNumber: '653781',
      phone: '010-6405-4130',
      vehicleType: '',
      inspectionType: '',
      visitStatus: ''
    },
    {
      id: 6,
      status: '',
      inspectionDate: '2025-07-03',
      displayDate: '07.03()',
      time: ' 9:00',
      vehicleNumber: '326936',
      phone: '010-4952-7782',
      vehicleType: '',
      inspectionType: '',
      visitStatus: ''
    },
    {
      id: 7,
      status: '',
      inspectionDate: '2025-07-03',
      displayDate: '07.03()',
      time: ' 10:00',
      vehicleNumber: '309725',
      phone: '010-3737-1847',
      vehicleType: '',
      inspectionType: '',
      visitStatus: ''
    },
    {
      id: 8,
      status: '',
      inspectionDate: '2025-07-03',
      displayDate: '07.03()',
      time: ' 2:00',
      vehicleNumber: '478923',
      phone: '010-5062-8512',
      vehicleType: '',
      inspectionType: '',
      visitStatus: ''
    },
    {
      id: 9,
      status: '',
      inspectionDate: '2025-07-04',
      displayDate: '07.04()',
      time: ' 9:00',
      vehicleNumber: '570718',
      phone: '010-8784-2038',
      vehicleType: '',
      inspectionType: '',
      visitStatus: ''
    },
    {
      id: 10,
      status: '',
      inspectionDate: '2025-07-05',
      displayDate: '07.05()',
      time: ' 9:00',
      vehicleNumber: '099245',
      phone: '010-2557-3005',
      vehicleType: '',
      inspectionType: '',
      visitStatus: ' '
    }
  ]);

  //   
  const handleReservationConfirm = (id) => {
    setReservations(prev => prev.map(res => 
      res.id === id 
        ? { ...res, status: '' }
        : res
    ));
  };

  const handleReservationCancel = (id) => {
    setReservations(prev => prev.map(res => 
      res.id === id 
        ? { ...res, status: '', visitStatus: '' }
        : res
    ));
  };

  const handleVisit = (id) => {
    setReservations(prev => prev.map(res => 
      res.id === id 
        ? { ...res, status: '', visitStatus: '' }
        : res
    ));
  };

  const handleNoVisit = (id) => {
    setReservations(prev => prev.map(res => 
      res.id === id 
        ? { ...res, status: '', visitStatus: '' }
        : res
    ));
  };

  //      
  const getActionButtons = (reservation) => {
    //    
    if (reservation.status === '' || 
        reservation.status === '' || 
        reservation.status === '') {
      return <span className="completed-text">{reservation.status}</span>;
    }

    //   
    if (reservation.inspectionType === '') {
      //   - /  
      if (reservation.status === '') {
        return (
          <div className="action-buttons">
            <button 
              className="action-btn confirm" 
              onClick={(e) => {
                e.stopPropagation();
                handleReservationConfirm(reservation.id);
              }}
            >
              
            </button>
            <button 
              className="action-btn cancel" 
              onClick={(e) => {
                e.stopPropagation();
                handleReservationCancel(reservation.id);
              }}
            >
              
            </button>
          </div>
        );
      }
      //   - /  
      else if (reservation.status === '') {
        return (
          <div className="action-buttons">
            <button 
              className="action-btn visit" 
              onClick={(e) => {
                e.stopPropagation();
                handleVisit(reservation.id);
              }}
            >
              
            </button>
            <button 
              className="action-btn no-visit" 
              onClick={(e) => {
                e.stopPropagation();
                handleNoVisit(reservation.id);
              }}
            >
              
            </button>
          </div>
        );
      }
    }
    
    //  ()  -  / 
    if (reservation.inspectionType === '' && reservation.status === '') {
      return (
        <div className="action-buttons">
          <button 
            className="action-btn visit" 
            onClick={(e) => {
              e.stopPropagation();
              handleVisit(reservation.id);
            }}
          >
            
          </button>
          <button 
            className="action-btn no-visit" 
            onClick={(e) => {
              e.stopPropagation();
              handleNoVisit(reservation.id);
            }}
          >
            
          </button>
        </div>
      );
    }

    return <span className="completed-text">-</span>;
  };

  //  
  const handleSort = (key) => {
    let direction = 'asc';
    if (sortConfig.key === key && sortConfig.direction === 'asc') {
      direction = 'desc';
    }
    setSortConfig({ key, direction });
  };

  //  
  const sortedReservations = useMemo(() => {
    let sortableItems = [...reservations];
    
    if (sortConfig.key) {
      sortableItems.sort((a, b) => {
        let aValue = a[sortConfig.key];
        let bValue = b[sortConfig.key];
        
        //   
        if (sortConfig.key === 'status') {
          const statusOrder = { 
            '': 1, 
            '': 2, 
            '': 3,
            '': 4,
            '': 5
          };
          aValue = statusOrder[aValue] || 999;
          bValue = statusOrder[bValue] || 999;
        } else if (sortConfig.key === 'inspectionType') {
          const typeOrder = { '': 1, '': 2 };
          aValue = typeOrder[aValue] || 999;
          bValue = typeOrder[bValue] || 999;
        } else if (sortConfig.key === 'visitStatus') {
          const visitOrder = { ' ': 1, '': 2, '': 3 };
          aValue = visitOrder[aValue] || 999;
          bValue = visitOrder[bValue] || 999;
        } else if (sortConfig.key === 'inspectionDate' || sortConfig.key === 'time') {
          // /  
          if (aValue < bValue) return sortConfig.direction === 'asc' ? 1 : -1;
          if (aValue > bValue) return sortConfig.direction === 'asc' ? -1 : 1;
          return 0;
        }
        
        if (aValue < bValue) {
          return sortConfig.direction === 'asc' ? -1 : 1;
        }
        if (aValue > bValue) {
          return sortConfig.direction === 'asc' ? 1 : -1;
        }
        return 0;
      });
    }
    
    return sortableItems;
  }, [reservations, sortConfig]);

  const handleDateSelect = (year, month, day) => {
    if (dateModalType === 'start') {
      setStartDate({ year, month, day });
    } else {
      setEndDate({ year, month, day });
    }
    setShowDateModal(false);
  };

  const handleRowClick = (reservation) => {
    setSelectedReservation(reservation);
    setShowDetailModal(true);
  };

  const getStatusColor = (status) => {
    switch (status) {
      case '': return '#ffc107';
      case '': return '#28a745';
      case '': return '#dc3545';
      case '': return '#007bff';
      case '': return '#6c757d';
      default: return '#000';
    }
  };

  //   
  const getSortIcon = (columnKey) => {
    if (sortConfig.key !== columnKey) {
      return <span className="sort-icon"></span>;
    }
    return sortConfig.direction === 'asc' 
      ? <span className="sort-icon active"></span> 
      : <span className="sort-icon active"></span>;
  };

  //     
  const handleModalAction = (action, reservationId) => {
    switch (action) {
      case 'confirm':
        handleReservationConfirm(reservationId);
        break;
      case 'cancel':
        handleReservationCancel(reservationId);
        break;
      case 'visit':
        handleVisit(reservationId);
        break;
      case 'noVisit':
        handleNoVisit(reservationId);
        break;
      default:
        break;
    }
    setShowDetailModal(false);
  };

  return (
    <div className="c-res-01-01">
      <div className="page-header">
        <h1>  </h1>
      </div>

      {/*   */}
      <div className="search-filters">
        <div className="filter-row">
          <div className="filter-group">
            <select 
              value={selectedDateType} 
              onChange={(e) => setSelectedDateType(e.target.value)}
              className="date-type-select"
            >
              <option value="inspection"></option>
              <option value="visit"></option>
            </select>
          </div>

          <div className="date-range">
            <button 
              className="date-button"
              onClick={() => {
                setDateModalType('start');
                setShowDateModal(true);
              }}
            >
              {startDate.year}.{String(startDate.month).padStart(2, '0')}.{String(startDate.day).padStart(2, '0')}
            </button>
            <span className="date-separator">~</span>
            <button 
              className="date-button"
              onClick={() => {
                setDateModalType('end');
                setShowDateModal(true);
              }}
            >
              {endDate.year}.{String(endDate.month).padStart(2, '0')}.{String(endDate.day).padStart(2, '0')}
            </button>
          </div>

          <div className="filter-group">
            <select 
              value={searchType}
              onChange={(e) => setSearchType(e.target.value)}
              className="search-type-select"
            >
              <option value="all"></option>
              <option value="vehicle"></option>
              <option value="phone"></option>
            </select>
          </div>

          <div className="search-input-group">
            <input
              type="text"
              placeholder=" ..."
              value={searchKeyword}
              onChange={(e) => setSearchKeyword(e.target.value)}
              className="search-input"
            />
            <button className="search-button"></button>
          </div>
        </div>
      </div>

      {/*    */}
      <div className="reservation-table">
        <table>
          <thead>
            <tr>
              <th onClick={() => handleSort('status')} className="sortable">
                 {getSortIcon('status')}
              </th>
              <th onClick={() => handleSort('inspectionDate')} className="sortable">
                 {getSortIcon('inspectionDate')}
              </th>
              <th onClick={() => handleSort('time')} className="sortable">
                 {getSortIcon('time')}
              </th>
              <th></th>
              <th></th>
              <th></th>
              <th onClick={() => handleSort('inspectionType')} className="sortable">
                  {getSortIcon('inspectionType')}
              </th>
              <th onClick={() => handleSort('visitStatus')} className="sortable">
                 {getSortIcon('visitStatus')}
              </th>
            </tr>
          </thead>
          <tbody>
            {sortedReservations.map((reservation) => (
              <tr 
                key={reservation.id}
                onClick={() => handleRowClick(reservation)}
                className="clickable-row"
              >
                <td>
                  <span 
                    className="status-badge"
                    style={{ color: getStatusColor(reservation.status) }}
                  >
                     {reservation.status}
                  </span>
                </td>
                <td>{reservation.displayDate}</td>
                <td>{reservation.time}</td>
                <td>{reservation.vehicleNumber}</td>
                <td>{reservation.phone}</td>
                <td>{reservation.vehicleType}</td>
                <td>{reservation.inspectionType}</td>
                <td>
                  {getActionButtons(reservation)}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/*  */}
      <div className="pagination">
        <button className="page-btn">&lt;</button>
        <button className="page-btn active">1</button>
        <button className="page-btn">2</button>
        <button className="page-btn">3</button>
        <button className="page-btn">&gt;</button>
      </div>

      {/*    */}
      <DateModal
        isOpen={showDateModal}
        onClose={() => setShowDateModal(false)}
        onConfirm={handleDateSelect}
        initialDate={dateModalType === 'start' ? startDate : endDate}
        title=" "
        mode="day"
      />

      {/*    */}
      {showDetailModal && selectedReservation && (
        <ReservationDetailModal
          reservation={reservations.find(r => r.id === selectedReservation.id)}
          onClose={() => setShowDetailModal(false)}
          onAction={handleModalAction}
        />
      )}
    </div>
  );
}

//    
function ReservationDetailModal({ reservation, onClose, onAction }) {
  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="modal-content" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h2> </h2>
          <button className="modal-close" onClick={onClose}></button>
        </div>
        
        <div className="modal-body">
          <div className="detail-section">
            <h3>[ ]</h3>
            <p>: {reservation.status}</p>
            <p>: 2025-07-29 {reservation.time}</p>
            <p>: 20250729-0012</p>
          </div>

          <div className="detail-section">
            <h3>[/]</h3>
            <p>/: 2025-07-29 09:45</p>
          </div>

          <div className="detail-section">
            <h3>[ ]</h3>
            <p>- </p>
          </div>

          <div className="detail-section">
            <h3>[ ]</h3>
            <p>: </p>
            <p>: {reservation.phone}</p>
          </div>

          <div className="detail-section">
            <h3>[ ]</h3>
            <p>: {reservation.vehicleNumber}</p>
            <p>: {reservation.vehicleType}</p>
          </div>

          <div className="detail-section">
            <h3>[ ]</h3>
            <p>:  </p>
          </div>
        </div>

        <div className="modal-footer">
          {reservation.status === '' && reservation.inspectionType === '' && (
            <>
              <button 
                className="modal-btn confirm"
                onClick={() => onAction('confirm', reservation.id)}
              >
                
              </button>
              <button 
                className="modal-btn cancel"
                onClick={() => onAction('cancel', reservation.id)}
              >
                
              </button>
            </>
          )}
          {reservation.status === '' && (
            <>
              <button 
                className="modal-btn visit"
                onClick={() => onAction('visit', reservation.id)}
              >
                
              </button>
              <button 
                className="modal-btn no-visit"
                onClick={() => onAction('noVisit', reservation.id)}
              >
                
              </button>
              <button 
                className="modal-btn cancel"
                onClick={() => onAction('cancel', reservation.id)}
              >
                
              </button>
            </>
          )}
        </div>
      </div>
    </div>
  );
}

export default CRes0101;