import React, { useState } from 'react';

function SNSModal({ isOpen, onClose, onConfirm }) {
  const [snsData, setSnsData] = useState({
    type: '',
    url: ''
  });

  if (!isOpen) return null;

  // URL   
  const isValidUrl = (urlString) => {
    try {
      new URL(urlString);
      return true;
    } catch {
      //    
      const domainPattern = /^[a-zA-Z0-9][a-zA-Z0-9-]{0,61}[a-zA-Z0-9]?\.[a-zA-Z]{2,}$/;
      return domainPattern.test(urlString);
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (snsData.type.trim() && snsData.url.trim()) {
      let finalUrl = snsData.url.trim();
      
      // http://  https://   
      if (!finalUrl.startsWith('http://') && !finalUrl.startsWith('https://')) {
        finalUrl = 'https://' + finalUrl;
      }
      
      // URL  
      if (!isValidUrl(finalUrl)) {
        alert(' URL  . (: caryak.net)');
        return;
      }
      
      onConfirm({
        type: snsData.type,
        url: finalUrl
      });
      setSnsData({ type: '', url: '' });
    } else {
      alert('SNS  URL  .');
    }
  };

  const handleClose = () => {
    setSnsData({ type: '', url: '' });
    onClose();
  };

  return (
    <div className="modal-overlay" onClick={handleClose}>
      <div className="modal-content" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h3>SNS </h3>
          <button className="modal-close" onClick={handleClose}>
            
          </button>
        </div>
        
        <form onSubmit={handleSubmit}>
          <div className="modal-body">
            <div className="form-group">
              <label>,  SNS  </label>
              <input
                type="text"
                value={snsData.type}
                onChange={(e) => setSnsData({...snsData, type: e.target.value})}
                placeholder=": "
                required
              />
            </div>
            
            <div className="form-group">
              <label>SNS url </label>
              <input
                type="text"
                value={snsData.url}
                onChange={(e) => setSnsData({...snsData, url: e.target.value})}
                placeholder=": caryak.net  https://caryak.net"
                required
              />
              <p className="help-text">http:// https://  .</p>
            </div>
          </div>
          
          <div className="modal-footer">
            <button type="button" className="btn-cancel" onClick={handleClose}>
              
            </button>
            <button type="submit" className="btn-confirm">
              
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}

export default SNSModal;
