import React from 'react';
import './VehicleNumberInput.css';

const VehicleNumberInput = ({ value = '', onChange, required = true }) => {
  const handleKeyUp = (e) => {
    let inputValue = e.target.value;
    
    //   
    const cleaned = inputValue.replace(/[^0-9-]/g, '');
    
    //  8
    const limited = cleaned.slice(0, 8);
    
    //    
    if (limited !== inputValue) {
      e.target.value = limited;
    }
    
    onChange(limited);
  };

  return (
    <input
      type="text"
      className="form-input"
      placeholder="121234"
      defaultValue={value}
      onKeyUp={handleKeyUp}  // keyup  
      onPaste={handleKeyUp}  //  
      required={required}
      autoComplete="off"
    />
  );
};

export default VehicleNumberInput;