import React, { useState, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import VehicleNumberInput from '../../components/VehicleNumberInput';
import PhoneNumberInput from '../../components/PhoneNumberInput';
import VehicleInfoModal from '../../components/VehicleInfoModal';
import ConfirmModal from '../../components/ConfirmModal';
import MaintenanceReport from './MaintenanceReport';
import './c-res-02-01.css';
import { ENGINE_OIL_DATA } from '../../data/engineOilData';
import { DISCOUNT_POLICIES } from '../../data/discountData';

function CRes0201() {
  const navigate = useNavigate();
  const [step, setStep] = useState(1); // 1: , 2: , 3: 
  const [customerData, setCustomerData] = useState({
    vehicleNumber: '',
    phoneNumber: '',
    name: '',
    isNew: false,
    conflictData: null,
    previousMileage: 85000 //   ()
  });
  const [isVehicleValid, setIsVehicleValid] = useState(true);
  const [mileage, setMileage] = useState('');
  const [mileageDisplay, setMileageDisplay] = useState('');
  const [tempMileage, setTempMileage] = useState(''); //  
  const [maintenanceItems, setMaintenanceItems] = useState([]);
  const [showModal, setShowModal] = useState(null);
  const [showVehicleModal, setShowVehicleModal] = useState(false);
  const [vehicleInfo, setVehicleInfo] = useState({
    type: '',
    size: '',
    fuel: '',
    inspectionType: null
  });
  const [newItem, setNewItem] = useState({
    name: '',
    quantity: 1,
    price: ''
  });
  const [showNewItemRow, setShowNewItemRow] = useState(false);

  // Refs for input focus management
  const nameInputRef = useRef(null);
  const quantityInputRef = useRef(null);
  const priceInputRef = useRef(null);

  //   
  const OTHER_SERVICES = {
    inspection: { name: '  ', price: 35000 },
    airFilter: { name: ' ', price: 25000 }
  };

  //   
  const formatMileage = (value) => {
    const numbers = value.replace(/[^\d]/g, '');
    if (!numbers) return '';
    return numbers.replace(/\B(?=(\d{3})+(?!\d))/g, ',');
  };

  //   
  const handleMileageChange = (e) => {
    const inputValue = e.target.value;
    const numbers = inputValue.replace(/[^\d]/g, '');
    
    if (numbers.length > 9) return;
    
    const formatted = formatMileage(numbers);
    setMileageDisplay(formatted);
    setTempMileage(numbers); //  
  };

  const handleMileageKeyDown = (e) => {
    if (e.key === 'Enter') {
      e.preventDefault();
      validateAndSetMileage();
    }
  };

  const handleMileageBlur = () => {
    validateAndSetMileage();
  };

  //    
  const validateAndSetMileage = () => {
    if (!tempMileage) return;
    
    const newMileage = parseInt(tempMileage);
    const previousMileage = customerData.previousMileage || 0;
    
    if (newMileage < previousMileage) {
      setShowModal('mileageWarning');
    } else {
      setMileage(tempMileage);
      console.log('  :', tempMileage);
    }
  };

  //    
  const handleMileageWarningConfirm = () => {
    setMileage(tempMileage);
    setShowModal(null);
    console.log('  :', tempMileage);
  };

  //    
  const handleMileageWarningCancel = () => {
    //   
    setMileageDisplay(formatMileage(customerData.previousMileage.toString()));
    setTempMileage(customerData.previousMileage.toString());
    setMileage(customerData.previousMileage.toString());
    setShowModal(null);
  };

  //   -   
  const handleCustomerSearch = () => {
    if (!isVehicleValid || !customerData.vehicleNumber || !customerData.phoneNumber) {
      return;
    }
    
    const { vehicleNumber, phoneNumber } = customerData;
    
    //  2:   (121234 +  )
    if (vehicleNumber === '121234' && phoneNumber !== '010-9876-5432') {
      setCustomerData(prev => ({ 
        ...prev, 
        conflictData: {
          name: '',
          phone: '010-1111-2222'
        }
      }));
      setShowModal('conflict');
      return;
    }
    
    //  3:   +  (010-9876-5432 +  )
    if (phoneNumber === '010-9876-5432' && vehicleNumber !== '121234') {
      setCustomerData(prev => ({ 
        ...prev, 
        name: '',
        isNew: false,
        previousMileage: 0 //   0
      }));
      setShowModal('newVehicle');
      return;
    }
    
    //  4:   (121234 + 010-9876-5432)
    if (vehicleNumber === '121234' && phoneNumber === '010-9876-5432') {
      setCustomerData(prev => ({ 
        ...prev, 
        name: '',
        isNew: false,
        previousMileage: 85000 //  
      }));
      setStep(2);
      return;
    }
    
    //  1:   (   )
    setCustomerData(prev => ({ 
      ...prev, 
      isNew: true, 
      name: '',
      previousMileage: 0 //   0
    }));
    setStep(2);
  };

  //    
  const getQuickItems = () => {
    const items = [];
    
    //   
    if (vehicleInfo.inspectionType?.prices) {
      const inspectionPrice = vehicleInfo.inspectionType.prices.regular || 0;
      if (inspectionPrice > 0) {
        items.push({
          name: `   - ${vehicleInfo.inspectionType.name}`,
          category: 'A',
          price: inspectionPrice + 10000
        });
      }
    } else {
      items.push({
        name: OTHER_SERVICES.inspection.name,
        category: 'A',
        price: OTHER_SERVICES.inspection.price
      });
    }

    // 
    if (vehicleInfo.type && vehicleInfo.size && vehicleInfo.fuel) {
      const ecoOil = ENGINE_OIL_DATA.eco[vehicleInfo.type]?.[vehicleInfo.size]?.[vehicleInfo.fuel];
      const standardOil = ENGINE_OIL_DATA.standard[vehicleInfo.type]?.[vehicleInfo.size]?.[vehicleInfo.fuel];
      
      if (ecoOil) {
        items.push({
          name: `() - ${vehicleInfo.type}/${vehicleInfo.size}`,
          category: 'A',
          price: ecoOil.price,
          oilType: 'eco',
          details: `${ecoOil.liter}L, ${ecoOil.viscosity}`
        });
      }
      
      if (standardOil) {
        items.push({
          name: `() - ${vehicleInfo.type}/${vehicleInfo.size}`,
          category: 'A',
          price: standardOil.price,
          oilType: 'standard',
          details: `${standardOil.liter}L, ${standardOil.viscosity}`
        });
      }
    } else {
      items.push(
        {
          name: '()',
          category: 'A',
          price: 75000,
          oilType: 'eco'
        },
        {
          name: '()',
          category: 'A',
          price: 120000,
          oilType: 'standard'
        }
      );
    }

    //  
    items.push({
      name: OTHER_SERVICES.airFilter.name,
      category: 'A',
      price: OTHER_SERVICES.airFilter.price
    });

    //  
    items.push({
      name: '',
      category: 'custom',
      price: 0
    });

    return items;
  };

  //   
  const addQuickItem = (item) => {
    if (item.category === 'custom') {
      //        
      setShowNewItemRow(true);
      setTimeout(() => {
        nameInputRef.current?.focus();
      }, 100);
      return;
    }

    const isDuplicate = maintenanceItems.some(
      existingItem => existingItem.name === item.name
    );
    
    if (!isDuplicate) {
      setMaintenanceItems(prev => [...prev, {
        id: Date.now(),
        name: item.name,
        category: item.category,
        quantity: 1,
        price: item.price,
        total: item.price,
        oilType: item.oilType || null,
        details: item.details || null
      }]);
    }
  };

  //    
  const handleNewItemAdd = () => {
    if (newItem.name && newItem.price) {
      const price = parseInt(newItem.price.replace(/[^0-9]/g, '')) || 0;
      setMaintenanceItems(prev => [...prev, {
        id: Date.now(),
        name: newItem.name,
        category: 'C', //   C 
        quantity: newItem.quantity,
        price: price,
        total: newItem.quantity * price
      }]);
      
      // 
      setNewItem({ name: '', quantity: 1, price: '' });
      setShowNewItemRow(false);
    }
  };

  //    
  const handleNewItemChange = (field, value) => {
    if (field === 'quantity') {
      const num = parseInt(value) || 1;
      setNewItem(prev => ({ ...prev, quantity: Math.max(1, num) }));
    } else if (field === 'price') {
      const formatted = value.replace(/[^0-9]/g, '');
      setNewItem(prev => ({ ...prev, price: formatted }));
    } else {
      setNewItem(prev => ({ ...prev, [field]: value }));
    }
  };

  //     
  const handleNewItemKeyDown = (e, field) => {
    if (e.key === 'Enter' || e.key === 'Tab') {
      e.preventDefault();
      if (field === 'name') {
        quantityInputRef.current?.focus();
      } else if (field === 'quantity') {
        priceInputRef.current?.focus();
      } else if (field === 'price') {
        handleNewItemAdd();
      }
    } else if (e.key === 'Escape') {
      setNewItem({ name: '', quantity: 1, price: '' });
      setShowNewItemRow(false);
    }
  };

  //  
  const calculateDiscount = () => {
    let totalDiscount = 0;
    const itemNames = maintenanceItems.map(item => item.name);
    
    for (const policy of DISCOUNT_POLICIES) {
      if (!policy.enabled) continue;
      
      const hasAllItems = policy.items.every(policyItem => {
        return itemNames.some(itemName => {
          const lowerItemName = itemName.toLowerCase();
          const lowerPolicyItem = policyItem.toLowerCase();
          
          if (lowerPolicyItem === '') return lowerItemName.includes('');
          if (lowerPolicyItem === '') return lowerItemName.includes('');
          if (lowerPolicyItem === '') return lowerItemName.includes('');
          if (lowerPolicyItem === '') return lowerItemName.includes('');
          if (lowerPolicyItem.includes('')) {
            return lowerItemName.includes('') && 
                   lowerItemName.includes(lowerPolicyItem.includes('') ? '' : '');
          }
          return lowerItemName.includes(lowerPolicyItem);
        });
      });
      
      if (hasAllItems) {
        totalDiscount = Math.max(totalDiscount, policy.discount);
      }
    }
    
    return totalDiscount;
  };

  //    
  const updateQuantity = (id, change) => {
    setMaintenanceItems(prev => prev.map(item => {
      if (item.id === id) {
        const newQuantity = Math.max(1, item.quantity + change);
        return {
          ...item,
          quantity: newQuantity,
          total: newQuantity * item.price
        };
      }
      return item;
    }));
  };

  //    
  const updatePrice = (id, newPrice) => {
    setMaintenanceItems(prev => prev.map(item => {
      if (item.id === id) {
        const price = parseInt(newPrice.replace(/[^0-9]/g, '')) || 0;
        return {
          ...item,
          price: price,
          total: item.quantity * price
        };
      }
      return item;
    }));
  };

  //   
  const deleteItem = (id) => {
    setMaintenanceItems(prev => prev.filter(item => item.id !== id));
  };

  //  
  const subtotal = maintenanceItems.reduce((sum, item) => sum + item.total, 0);
  const discount = calculateDiscount();
  const totalAmount = subtotal - discount;

  // 
  const handleBack = () => {
    if (step > 1 && maintenanceItems.length > 0) {
      setShowModal('unsaved');
    } else if (step > 1) {
      setStep(1);
      // 
      setMaintenanceItems([]);
      setMileage('');
      setMileageDisplay('');
      setTempMileage('');
      setVehicleInfo({
        type: '',
        size: '',
        fuel: '',
        inspectionType: null
      });
      setNewItem({ name: '', quantity: 1, price: '' });
      setShowNewItemRow(false);
    }
  };

  //  
  const handleMaintenanceRegister = () => {
    if (maintenanceItems.length === 0) return;
    
    //    
    console.log('  :', {
      customerData, 
      maintenanceItems, 
      totalAmount,
      vehicleInfo,
      discount,
      mileage
    });
    
    setStep(3);
  };

  //   
  const handleReportBack = () => {
    setStep(2);
  };

  //   
  const handleReportComplete = () => {
    navigate('/c-home-01');
  };

  //   
  const handleVehicleInfoConfirm = (updatedInfo) => {
    setVehicleInfo(updatedInfo);
    setShowVehicleModal(false);
  };

  //   
  const handleConflictConfirm = () => {
    // /  
    setCustomerData(prev => ({ 
      ...prev, 
      isNew: false,
      conflictData: null 
    }));
    setStep(2);
    setShowModal(null);
    alert(' () "  "  .');
  };

  const handleConflictCancel = () => {
    //    -   
    setCustomerData(prev => ({ 
      ...prev, 
      isNew: true,
      conflictData: null 
    }));
    setStep(2);
    setShowModal(null);
    alert(' ()   , " "   .');
  };

  const handleNewVehicleConfirm = () => {
    //   
    setCustomerData(prev => ({ ...prev, isNew: false }));
    setStep(2);
    setShowModal(null);
  };

  const handleUnsavedConfirm = () => {
    //   
    setShowModal(null);
    setStep(1);
    setMaintenanceItems([]);
    setMileage('');
    setMileageDisplay('');
    setTempMileage('');
    setVehicleInfo({
      type: '',
      size: '',
      fuel: '',
      inspectionType: null
    });
    setNewItem({ name: '', quantity: 1, price: '' });
    setShowNewItemRow(false);
  };

  const handleUnsavedCancel = () => {
    //   
    setShowModal(null);
    setStep(1);
    setMaintenanceItems([]);
    setMileage('');
    setMileageDisplay('');
    setTempMileage('');
    setVehicleInfo({
      type: '',
      size: '',
      fuel: '',
      inspectionType: null
    });
    setNewItem({ name: '', quantity: 1, price: '' });
    setShowNewItemRow(false);
  };

  const handleCancel = () => {
    if (maintenanceItems.length > 0) {
      if (window.confirm('   .  ?')) {
        navigate('/c-home-01');
      }
    } else {
      navigate('/c-home-01');
    }
  };

  const handleSave = () => {
    //    
    alert(' .');
  };

  //    ( )
  const handleAddItem = () => {
    setShowNewItemRow(true);
    setTimeout(() => {
      nameInputRef.current?.focus();
    }, 100);
  };

  return (
    <div className="c-res-02-01">
      {step === 1 && (
        // 1:  
        <>
          <div className="page-header">
            <h1> </h1>
          </div>
          
          <div className="customer-search">
            <form className="search-form" onSubmit={(e) => { e.preventDefault(); handleCustomerSearch(); }}>
              <div className="form-group">
                <label className="form-label"> .</label>
                <VehicleNumberInput
                  value={customerData.vehicleNumber}
                  onChange={(value) => setCustomerData(prev => ({...prev, vehicleNumber: value}))}
                  onValidate={setIsVehicleValid}
                />
                <div className="test-hint">(   121234 .)</div>
              </div>
              
              <div className="form-group">
                <label className="form-label">  .</label>
                <PhoneNumberInput
                  value={customerData.phoneNumber}
                  onChange={(value) => setCustomerData(prev => ({...prev, phoneNumber: value}))}
                />
                <div className="test-hint">(  010-9876-5432 .)</div>
              </div>
              
              <button 
                type="submit" 
                className="search-button"
                disabled={!isVehicleValid || !customerData.vehicleNumber || !customerData.phoneNumber}
              >
                
              </button>
            </form>
            
            {/*    */}
            <div className="test-scenarios">
              <h4>  </h4>
              <ul>
                <li>
                  <span className="scenario-title"> 1 ( )</span>: 
                        
                </li>
                <li>
                  <span className="scenario-title"> 2 ( )</span>: 
                  <span className="test-data">121234</span> +   
                  <br />  (/010-1111-2222)    
                </li>
                <li>
                  <span className="scenario-title"> 3 ( +)</span>: 
                  <span className="test-data">010-9876-5432</span> +   
                  <br />        
                </li>
                <li>
                  <span className="scenario-title"> 4 ( )</span>: 
                  <span className="test-data">121234</span> + <span className="test-data">010-9876-5432</span> 
                  <br />     
                </li>
              </ul>
            </div>
          </div>
        </>
      )}
      
      {step === 2 && (
        // 2:  
        <>
          <div className="global-header">
            <div className="header-nav">
              <button className="nav-button" onClick={handleBack}>
                &lt; 
              </button>
              <span className="vehicle-number">{customerData.vehicleNumber}</span>
              <button className="nav-button" disabled>
                 &gt;
              </button>
            </div>
          </div>

          <div className="maintenance-section">
            <div className="customer-info">
              <div style={{ display: 'flex', gap: '40px' }}>
                <div className="customer-details">
                  <span className="info-label"></span>
                  <span className="info-value">{customerData.vehicleNumber}</span>
                </div>
                <div className="customer-details">
                  <span className="info-label"></span>
                  <span className="info-value">
                    {customerData.name} ({customerData.phoneNumber})
                  </span>
                </div>
              </div>
              <button 
                className="vehicle-info-button"
                onClick={() => setShowVehicleModal(true)}
              >
                +   /
              </button>
            </div>

            <div className="mileage-section">
              <div className="form-group">
                <label className="form-label"> .</label>
                <div className="mileage-input-wrapper">
                  <input
                    type="text"
                    className="form-input mileage-input"
                    placeholder="85,000"
                    value={mileageDisplay}
                    onChange={handleMileageChange}
                    onKeyDown={handleMileageKeyDown}
                    onBlur={handleMileageBlur}
                    inputMode="numeric"
                  />
                  {mileageDisplay && <span className="mileage-unit">km</span>}
                </div>
                {customerData.previousMileage > 0 && (
                  <div className="test-hint">
                    ( : {customerData.previousMileage.toLocaleString()}km)
                  </div>
                )}
              </div>
            </div>

            <div className="quick-items">
              <h3>    (  )</h3>
              <div className="quick-items-grid">
                {getQuickItems().map((item, index) => (
                  <button
                    key={index}
                    className="quick-item-button"
                    onClick={() => addQuickItem(item)}
                    title={item.details || ''}
                  >
                    {item.name}
                  </button>
                ))}
              </div>
            </div>

            <div className="maintenance-list">
              <h3> </h3>
              
              {maintenanceItems.length === 0 && !showNewItemRow ? (
                <div style={{ textAlign: 'center', padding: '40px', color: '#6c757d' }}>
                         .
                </div>
              ) : (
                <>
                  {maintenanceItems.map(item => (
                    <div key={item.id} className="maintenance-item">
                      <div className="item-header">
                        <div>
                          <span className="item-name">{item.name}</span>
                          <span className="item-category">(: {item.category})</span>
                          {item.details && (
                            <span style={{ fontSize: '12px', color: '#6c757d', marginLeft: '8px' }}>
                              {item.details}
                            </span>
                          )}
                        </div>
                        <button 
                          className="delete-button"
                          onClick={() => deleteItem(item.id)}
                        >
                          
                        </button>
                      </div>
                      
                      <div className="item-details">
                        <div className="quantity-control">
                          <span className="info-label">:</span>
                          <button 
                            className="quantity-button"
                            onClick={() => updateQuantity(item.id, -1)}
                          >
                            -
                          </button>
                          <span className="quantity-value">{item.quantity}</span>
                          <button 
                            className="quantity-button"
                            onClick={() => updateQuantity(item.id, 1)}
                          >
                            +
                          </button>
                        </div>
                        
                        <div>
                          <span className="info-label">: </span>
                          <input
                            type="text"
                            className="form-input price-input"
                            value={item.price.toLocaleString()}
                            onChange={(e) => updatePrice(item.id, e.target.value)}
                          />
                          <span> </span>
                        </div>
                        
                        <div className="item-total">
                          : {item.total.toLocaleString()} 
                        </div>
                      </div>
                    </div>
                  ))}
                  
                  {showNewItemRow && (
                    <div className="maintenance-item new-item">
                      <div className="item-header">
                        <div style={{ flex: 1 }}>
                          <input
                            ref={nameInputRef}
                            type="text"
                            className="form-input"
                            placeholder=" "
                            value={newItem.name}
                            onChange={(e) => handleNewItemChange('name', e.target.value)}
                            onKeyDown={(e) => handleNewItemKeyDown(e, 'name')}
                            style={{ width: '100%' }}
                          />
                        </div>
                        <button 
                          className="delete-button"
                          onClick={() => {
                            setNewItem({ name: '', quantity: 1, price: '' });
                            setShowNewItemRow(false);
                          }}
                        >
                          
                        </button>
                      </div>
                      
                      <div className="item-details">
                        <div className="quantity-control">
                          <span className="info-label">:</span>
                          <button 
                            className="quantity-button"
                            onClick={() => handleNewItemChange('quantity', newItem.quantity - 1)}
                          >
                            -
                          </button>
                          <input
                            ref={quantityInputRef}
                            type="number"
                            className="quantity-input"
                            value={newItem.quantity}
                            onChange={(e) => handleNewItemChange('quantity', e.target.value)}
                            onKeyDown={(e) => handleNewItemKeyDown(e, 'quantity')}
                            style={{ width: '50px', textAlign: 'center' }}
                          />
                          <button 
                            className="quantity-button"
                            onClick={() => handleNewItemChange('quantity', newItem.quantity + 1)}
                          >
                            +
                          </button>
                        </div>
                        
                        <div>
                          <span className="info-label">: </span>
                          <input
                            ref={priceInputRef}
                            type="text"
                            className="form-input price-input"
                            placeholder="0"
                            value={newItem.price ? parseInt(newItem.price).toLocaleString() : ''}
                            onChange={(e) => handleNewItemChange('price', e.target.value)}
                            onKeyDown={(e) => handleNewItemKeyDown(e, 'price')}
                          />
                          <span> </span>
                        </div>
                        
                        <div className="item-total">
                          : {newItem.price ? (parseInt(newItem.price) * newItem.quantity).toLocaleString() : '0'} 
                        </div>
                      </div>
                    </div>
                  )}
                </>
              )}
              
              {!showNewItemRow && (
                <button className="add-item-button" onClick={handleAddItem}>
                  [   + ]
                </button>
              )}
            </div>

            <div className="total-section">
              {discount > 0 && (
                <>
                  <div style={{ textAlign: 'right', marginBottom: '8px' }}>
                    <span style={{ color: '#6c757d' }}>: {subtotal.toLocaleString()} </span>
                  </div>
                  <div style={{ textAlign: 'right', marginBottom: '8px' }}>
                    <span style={{ color: '#dc3545' }}>: -{discount.toLocaleString()} </span>
                  </div>
                </>
              )}
              <span className="total-label">: {totalAmount.toLocaleString()} </span>
            </div>

            <div className="action-buttons">
              <div className="button-row">
                <button className="action-button cancel" onClick={handleCancel}>
                  
                </button>
                <button className="action-button save" onClick={handleSave}>
                  
                </button>
              </div>
              <div className="button-row primary">
                <button 
                  className="action-button primary"
                  onClick={handleMaintenanceRegister}
                  disabled={maintenanceItems.length === 0}
                >
                   
                  <div className="button-subtext">(     )</div>
                </button>
              </div>
            </div>
          </div>
        </>
      )}
      
      {step === 3 && (
        // 3:  
        <MaintenanceReport
          customerData={customerData}
          maintenanceItems={maintenanceItems}
          vehicleInfo={vehicleInfo}
          mileage={mileage}
          onBack={handleReportBack}
          onComplete={handleReportComplete}
        />
      )}

      {/*    */}
      <ConfirmModal
        isOpen={showModal === 'conflict'}
        onClose={handleConflictCancel}
        onConfirm={handleConflictConfirm}
        title="  "
        message={
          <>
             ({customerData.vehicleNumber})  <br/>
            ({customerData.conflictData?.name}/{customerData.conflictData?.phone})  .<br/>
               ?
          </>
        }
        confirmText=", / "
        cancelText=",  "
      />

      {/*     */}
      <ConfirmModal
        isOpen={showModal === 'newVehicle'}
        onClose={() => setShowModal(null)}
        onConfirm={handleNewVehicleConfirm}
        title="  "
        message={
          <>
            {customerData.name} , !<br/>
             ({customerData.vehicleNumber}) ?
          </>
        }
        confirmText="  "
        cancelText=""
      />

      {/*    */}
      <ConfirmModal
        isOpen={showModal === 'unsaved'}
        onClose={handleUnsavedCancel}
        onConfirm={handleUnsavedConfirm}
        title="   "
        message={
          <>
                .<br/>
            ?
          </>
        }
        confirmText="  "
        cancelText="  "
        confirmButtonStyle="primary"
        cancelButtonStyle="secondary"
      />

      {/*    */}
      <ConfirmModal
        isOpen={showModal === 'mileageWarning'}
        onClose={handleMileageWarningCancel}
        onConfirm={handleMileageWarningConfirm}
        title="  "
        message={
          <>
                 .<br/>
            (: {customerData.previousMileage.toLocaleString()}km  : {parseInt(tempMileage).toLocaleString()}km)<br/>
            <br/>
              ?
          </>
        }
        confirmText=""
        cancelText=""
      />

      {/*     */}
      <VehicleInfoModal
        isOpen={showVehicleModal}
        onClose={() => setShowVehicleModal(false)}
        vehicleInfo={vehicleInfo}
        onVehicleInfoChange={(field, value) => {
          const newVehicleInfo = { ...vehicleInfo };
          if (field === 'type') {
            newVehicleInfo.type = value;
            newVehicleInfo.size = '';
          } else {
            newVehicleInfo[field] = value;
          }
          setVehicleInfo(newVehicleInfo);
        }}
        onConfirm={handleVehicleInfoConfirm}
      />
    </div>
  );
}

export default CRes0201;