import React, { useState, useEffect } from 'react';
import { NavLink, useNavigate, useLocation } from 'react-router-dom';
import './LNB.css';

function LNB() {
  const navigate = useNavigate();
  const location = useLocation();
  const [partnerType, setPartnerType] = useState(
    localStorage.getItem('partnerType') || 'carCenter'
  );
  
  //    
  const [mainPhotoPreview, setMainPhotoPreview] = useState(
    localStorage.getItem('mainPhotoPreview') || null
  );

  //   
  const togglePartnerType = () => {
    const newType = partnerType === 'carCenter' ? 'inspection' : 'carCenter';
    setPartnerType(newType);
    
    // localStorage 
    localStorage.setItem('partnerType', newType);
    
    //    (     )
    window.dispatchEvent(new Event('partnerTypeChanged'));
  };

  // localStorage   (   )
  useEffect(() => {
    const handleStorageChange = () => {
      const newType = localStorage.getItem('partnerType') || 'carCenter';
      if (newType !== partnerType) {
        setPartnerType(newType);
      }
    };

    window.addEventListener('storage', handleStorageChange);
    window.addEventListener('partnerTypeChanged', handleStorageChange);
    
    return () => {
      window.removeEventListener('storage', handleStorageChange);
      window.removeEventListener('partnerTypeChanged', handleStorageChange);
    };
  }, [partnerType]);

  //    
  useEffect(() => {
    const handleMainPhotoChange = () => {
      const newPhoto = localStorage.getItem('mainPhotoPreview');
      setMainPhotoPreview(newPhoto);
    };

    window.addEventListener('mainPhotoChanged', handleMainPhotoChange);
    
    return () => {
      window.removeEventListener('mainPhotoChanged', handleMainPhotoChange);
    };
  }, []);

  // /   
  const handleReservationClick = () => {
    //        
    if (partnerType === 'carCenter') {
      navigate('/c-res-02-01'); //  
    } else {
      navigate('/c-res-01-01'); //  
    }
  };

  //      
  const isParentActive = (subItems) => {
    if (!subItems) return false;
    return subItems.some(subItem => 
      subItem.enabled && location.pathname === subItem.path
    );
  };

  //    (     )
  const menuItems = [
    { 
      path: '/c-home-01', 
      icon: '', 
      label: ' ()',
      enabled: true 
    },
    { 
      path: '/reservation', //    
      icon: '', 
      label: '/ ',
      enabled: true,
      isParent: true, //   
      onClick: handleReservationClick, //   
      subItems: [
        { 
          path: '/c-res-02-01', 
          label: ' ',
          enabled: partnerType === 'carCenter'
        },
        { 
          path: '/c-res-02-02', 
          label: ' ',
          enabled: partnerType === 'carCenter'
        },
        { 
          path: '/c-res-01-01',  
          label: ' ',
          enabled: partnerType === 'inspection'
        },
        { 
          path: '/c-res-01-02',  
          label: '  ',
          enabled: partnerType === 'inspection',
          isNew: true, 
        },
      ]
    },
    { 
      path: '/c-store-01', 
      icon: '', 
      label: '  ',
      enabled: true,
      subItems: [
        { 
          path: '/c-store-01', 
          label: '  ',
          enabled: true
        },
        { 
          path: '/c-store-02', 
          label: ' ',
          enabled: true
        },
        { 
          path: '/c-store-03', 
          label: ' ',
          enabled: partnerType === 'inspection'
        }
      ]
    },
    { 
      path: '/c-cont-02',  
      icon: '', 
      label: ' ',
      enabled: true
    },
    { 
      path: '/c-pay-01', 
      icon: '', 
      label: ' ',
      enabled: true,
      subItems: [
        { 
          path: '/c-pay-01', 
          label: ' ',
          enabled: true
        },
        { 
          path: '/c-pay-02', 
          label: partnerType === 'carCenter' ? ' ' : ' ',
          enabled: true
        }
      ]
    },
    { 
      path: '/c-cs-02', 
      icon: '', 
      label: '1:1 ',
      enabled: true
    }
  ];

  return (
    <nav className="lnb">
      {/*   */}
      <div className="lnb-profile-card">
        <div className="profile-image">
          {mainPhotoPreview ? (
            <img 
              src={mainPhotoPreview} 
              alt="" 
              style={{
                width: '100%',
                height: '100%',
                objectFit: 'cover',
                borderRadius: '12px'
              }}
            />
          ) : (
            <span className="profile-icon"></span>
          )}
        </div>
        <div className="profile-info">
          <h3 onClick={togglePartnerType} className="profile-name">
            {partnerType === 'carCenter' ? ' ' : ' '}
          </h3>
          <div className="profile-details">
            <p className="profile-address">   123</p>
            <p className="profile-phone">02-888-7777</p>
          </div>
          <div className="profile-tags">
            <span className="tag primary">#</span>
            <span className="tag secondary">#/</span>
            <span className="tag secondary">#</span>
            <span className="tag link">#</span>
          </div>
        </div>
      </div>
      
      {/*  */}
      <ul className="lnb-menu">
        {menuItems.map((item) => {
          const hasActiveSubItem = isParentActive(item.subItems);
          
          return (
            <li key={item.label} className="lnb-menu-item">
              {item.enabled ? (
                item.isParent && item.onClick ? (
                  // /     
                  <button 
                    onClick={item.onClick}
                    className={`menu-button ${hasActiveSubItem ? 'active' : ''}`}
                  >
                    <span className="icon">{item.icon}</span>
                    <span className="label">{item.label}</span>
                  </button>
                ) : item.subItems ? (
                  //    
                  <NavLink 
                    to={item.path} 
                    className={({ isActive }) => 
                      isActive || hasActiveSubItem ? 'active' : ''
                    }
                  >
                    <span className="icon">{item.icon}</span>
                    <span className="label">{item.label}</span>
                  </NavLink>
                ) : (
                  //    
                  <NavLink 
                    to={item.path} 
                    className={({ isActive }) => isActive ? 'active' : ''}
                  >
                    <span className="icon">{item.icon}</span>
                    <span className="label">{item.label}</span>
                  </NavLink>
                )
              ) : (
                <div className="disabled-menu">
                  <span className="icon">{item.icon}</span>
                  <span className="label">{item.label}</span>
                </div>
              )}
              
              {item.subItems && (
                <ul className="lnb-submenu">
                  {item.subItems.map((subItem) => (
                    <li key={subItem.path} className={!subItem.enabled ? 'disabled' : ''}>
                      {subItem.enabled ? (
                        <NavLink 
                          to={subItem.path}
                          className={({ isActive }) => isActive ? 'sub-active' : ''}
                        >
                          {subItem.label}
                          {subItem.isNew && <span className="new-badge">NEW</span>}
                        </NavLink>
                      ) : (
                        <span className="disabled-submenu">
                          {subItem.label}
                        </span>
                      )}
                    </li>
                  ))}
                </ul>
              )}
            </li>
          );
        })}
      </ul>
    </nav>
  );
}

export default LNB;