// src/pages/c-home-01/c-home-01.js
import React, { useState } from 'react';
import './c-home-01.css';

function CHome01() {
  const [selectedDate, setSelectedDate] = useState(new Date());
  
  //  
  const formatDate = (date) => {
    const year = date.getFullYear();
    const month = date.getMonth() + 1;
    const day = date.getDate();
    const weekDay = ['', '', '', '', '', '', ''][date.getDay()];
    return `${year} ${month} ${day} (${weekDay})`;
  };

  //  
  const changeDate = (days) => {
    const newDate = new Date(selectedDate);
    newDate.setDate(newDate.getDate() + days);
    setSelectedDate(newDate);
  };

  //  
  const todayTasks = [
    { time: '10:00', type: '', customer: 'OO', carNumber: '123456', service: ' ' },
    { time: '11:00', type: '', customer: 'XX', carNumber: '789012', service: ' ' },
    { time: '13:30', type: '', customer: 'XX', carNumber: '345678', service: ' ' },
    { time: '14:00', type: '', customer: 'OO', carNumber: '561234', service: '  ' },
    { time: '15:30', type: '', customer: 'XX', carNumber: '905678', service: ' ' }
  ];

  const newInquiries = [
    { id: 1, type: '', content: '    .' },
    { id: 2, type: '', content: '   ?' }
  ];

  return (
    <div className="dashboard-container">
      {/*   */}
      <div className="date-selector">
        <button onClick={() => changeDate(-1)} className="date-arrow"></button>
        <h2>{formatDate(selectedDate)}</h2>
        <button onClick={() => changeDate(1)} className="date-arrow"></button>
      </div>

      {/*    */}
      <div className="metric-cards">
        <div className="metric-card new-reservation">
          <h3> </h3>
          <div className="metric-value">5 </div>
        </div>
        <div className="metric-card today-visit">
          <h3>  </h3>
          <div className="metric-value">12 </div>
        </div>
      </div>

      {/*    */}
      <div className="today-tasks">
        <div className="section-header">
          <h3>   (Today's Tasks)</h3>
        </div>
        <div className="task-list">
          {todayTasks.map((task, index) => (
            <div key={index} className="task-item">
              <span className="task-time">{task.time}</span>
              <span className={`task-type ${task.type === '' ? 'inspection' : 'maintenance'}`}>
                {task.type}
              </span>
              <span className="task-customer">{task.customer} ({task.carNumber})</span>
              <span className="task-service">- {task.service}</span>
            </div>
          ))}
        </div>
        <div className="section-footer">
          <a href="/c-res-01" className="more-link"> </a>
        </div>
      </div>

      {/*  1:1  */}
      <div className="new-inquiries">
        <div className="section-header">
          <h3> 1:1 </h3>
        </div>
        <div className="inquiry-list">
          {newInquiries.map((inquiry) => (
            <div key={inquiry.id} className="inquiry-item">
              <span className="inquiry-type">[{inquiry.type}]</span>
              <span className="inquiry-content">{inquiry.content}</span>
            </div>
          ))}
        </div>
        <div className="section-footer">
          <a href="/c-cs-02" className="more-link"> </a>
        </div>
      </div>
    </div>
  );
}

export default CHome01;