import React, { useState, useEffect } from 'react';
import ConfirmModal from '../../components/ConfirmModal';
import './MaintenanceReport.css';

const MaintenanceReport = ({ 
  customerData, 
  maintenanceItems, 
  vehicleInfo,
  mileage,
  onBack,
  onComplete 
}) => {
  const [preInspection, setPreInspection] = useState(''); // 'pass' or 'fail'
  const [reportItems, setReportItems] = useState([]);
  const [showModal, setShowModal] = useState(null);
  const [hasUnsavedChanges, setHasUnsavedChanges] = useState(false);

  //    
  const hasInspectionService = maintenanceItems.some(item => 
    item.name.includes(' ')
  );

  //   
  useEffect(() => {
    const initialItems = [
      // A.   
      {
        id: 'engine-oil',
        category: 'A',
        name: '',
        type: 'periodic',
        status: 'good', // 'good' or 'needChange'
        nextCheckMonths: 6,
        memo: '',
        icon: ''
      },
      {
        id: 'air-filter',
        category: 'A',
        name: ' ',
        type: 'periodic',
        status: 'good',
        nextCheckMonths: 6,
        memo: '',
        icon: ''
      },
      {
        id: 'brake-pad',
        category: 'A',
        name: ' ',
        type: 'periodic',
        status: 'good',
        nextCheckMonths: 12,
        memo: '',
        icon: ''
      },
      // B.   
      {
        id: 'exterior-light',
        category: 'B',
        name: ' ',
        type: 'status',
        status: 'good',
        memo: '',
        icon: ''
      },
      {
        id: 'tire',
        category: 'B',
        name: '',
        type: 'status',
        status: 'good',
        memo: '',
        icon: ''
      },
      {
        id: 'battery',
        category: 'B',
        name: '',
        type: 'status',
        status: 'good',
        memo: '',
        icon: ''
      }
    ];
    setReportItems(initialItems);
  }, []);

  //   
  const getNextCheckDate = (months) => {
    const date = new Date();
    date.setMonth(date.getMonth() + months);
    return date.toLocaleDateString('ko-KR', { 
      year: 'numeric', 
      month: 'long', 
      day: 'numeric' 
    });
  };

  //   
  const handlePreInspectionSelect = (result) => {
    setPreInspection(result);
    setHasUnsavedChanges(true);
    
    if (result === 'fail') {
      setShowModal('inspectionFail');
    }
  };

  //    
  const updateReportItem = (id, field, value) => {
    setReportItems(prev => prev.map(item => {
      if (item.id === id) {
        return { ...item, [field]: value };
      }
      return item;
    }));
    setHasUnsavedChanges(true);
  };

  //     
  const updateNextCheckMonths = (id, change) => {
    setReportItems(prev => prev.map(item => {
      if (item.id === id) {
        const newMonths = Math.max(0, Math.min(24, item.nextCheckMonths + change));
        return { ...item, nextCheckMonths: newMonths };
      }
      return item;
    }));
    setHasUnsavedChanges(true);
  };

  // 
  const handleBack = () => {
    if (hasUnsavedChanges) {
      setShowModal('unsaved');
    } else {
      onBack();
    }
  };

  // 
  const handleSave = () => {
    //   
    console.log(' :', {
      preInspection,
      reportItems,
      customerData,
      maintenanceItems,
      vehicleInfo,
      mileage
    });
    setHasUnsavedChanges(false);
    alert('.');
  };

  //  
  const handleComplete = () => {
    if (hasInspectionService && !preInspection) {
      alert('  .');
      return;
    }
    
    setShowModal('complete');
  };

  //  
  const handleCompleteConfirm = () => {
    console.log('  :', {
      preInspection,
      reportItems,
      customerData,
      maintenanceItems,
      vehicleInfo,
      mileage
    });
    
    //   
    alert('   .');
    
    //  
    onComplete();
  };

  //   
  const handleInspectionFailConfirm = () => {
    setShowModal(null);
    //    
    alert('    .');
    //  
    onComplete();
  };

  return (
    <div className="maintenance-report">
      <div className="global-header">
        <div className="header-nav">
          <button className="nav-button" onClick={handleBack}>
            &lt; 
          </button>
          <span className="vehicle-number">{customerData.vehicleNumber}</span>
          <button className="nav-button" disabled>
             &gt;
          </button>
        </div>
      </div>

      <div className="report-content">
        {/* 1.   (   ) */}
        {hasInspectionService && (
          <div className="report-section">
            <h2>1.   (   )</h2>
            <div className="section-content">
              <p className="section-description">
                    .<br />
                  .
              </p>
              <div className="inspection-buttons">
                <button
                  className={`inspection-button ${preInspection === 'pass' ? 'selected' : ''}`}
                  onClick={() => handlePreInspectionSelect('pass')}
                >
                   
                </button>
                <button
                  className={`inspection-button ${preInspection === 'fail' ? 'selected' : ''}`}
                  onClick={() => handlePreInspectionSelect('fail')}
                >
                   
                </button>
              </div>
            </div>
          </div>
        )}

        {/* 2.    */}
        <div className="report-section">
          <h2>2.   </h2>
          
          {/* (A)    */}
          <div className="report-subsection">
            <h3>(A)    (  )</h3>
            {reportItems.filter(item => item.category === 'A').map(item => (
              <div key={item.id} className="report-item">
                <h4><span className="item-icon">{item.icon}</span> {item.name}</h4>
                <div className="status-selection">
                  <input
                    type="radio"
                    id={`${item.id}-good`}
                    name={`status-${item.id}`}
                    checked={item.status === 'good'}
                    onChange={() => updateReportItem(item.id, 'status', 'good')}
                  />
                  <label htmlFor={`${item.id}-good`}>
                     
                  </label>
                  
                  <input
                    type="radio"
                    id={`${item.id}-needChange`}
                    name={`status-${item.id}`}
                    checked={item.status === 'needChange'}
                    onChange={() => updateReportItem(item.id, 'status', 'needChange')}
                  />
                  <label htmlFor={`${item.id}-needChange`}>
                     
                  </label>
                </div>
                
                {item.status === 'good' && (
                  <div className="next-check-setting">
                    <div className="month-selector">
                      <button 
                        className="month-button"
                        onClick={() => updateNextCheckMonths(item.id, -3)}
                      >
                        -
                      </button>
                      <span className="month-value">{item.nextCheckMonths}</span>
                      <button 
                        className="month-button"
                        onClick={() => updateNextCheckMonths(item.id, 3)}
                      >
                        +
                      </button>
                      <button 
                        className={`month-preset ${item.nextCheckMonths === 3 ? 'active' : ''}`}
                        onClick={() => updateReportItem(item.id, 'nextCheckMonths', 3)}
                      >
                        3
                      </button>
                      <button 
                        className={`month-preset ${item.nextCheckMonths === 6 ? 'active' : ''}`}
                        onClick={() => updateReportItem(item.id, 'nextCheckMonths', 6)}
                      >
                        6
                      </button>
                      <button 
                        className={`month-preset ${item.nextCheckMonths === 9 ? 'active' : ''}`}
                        onClick={() => updateReportItem(item.id, 'nextCheckMonths', 9)}
                      >
                        9
                      </button>
                      <button 
                        className={`month-preset ${item.nextCheckMonths === 12 ? 'active' : ''}`}
                        onClick={() => updateReportItem(item.id, 'nextCheckMonths', 12)}
                      >
                        12
                      </button>
                      <span className="month-label">   </span>
                    </div>
                    <div className="next-date">
                       : {getNextCheckDate(item.nextCheckMonths)}
                    </div>
                  </div>
                )}
                
                {item.status === 'needChange' && (
                  <textarea
                    className="memo-input"
                    placeholder="    "
                    value={item.memo}
                    onChange={(e) => updateReportItem(item.id, 'memo', e.target.value)}
                  />
                )}
              </div>
            ))}
          </div>

          {/* (B)    */}
          <div className="report-subsection">
            <h3>(B)    (  )</h3>
            {reportItems.filter(item => item.category === 'B').map(item => (
              <div key={item.id} className="report-item">
                <h4><span className="item-icon">{item.icon}</span> {item.name}</h4>
                <div className="status-selection">
                  <input
                    type="radio"
                    id={`${item.id}-good`}
                    name={`status-${item.id}`}
                    checked={item.status === 'good'}
                    onChange={() => updateReportItem(item.id, 'status', 'good')}
                  />
                  <label htmlFor={`${item.id}-good`}>
                     
                  </label>
                  
                  <input
                    type="radio"
                    id={`${item.id}-needChange`}
                    name={`status-${item.id}`}
                    checked={item.status === 'needChange'}
                    onChange={() => updateReportItem(item.id, 'status', 'needChange')}
                  />
                  <label htmlFor={`${item.id}-needChange`}>
                     
                  </label>
                </div>
                
                {item.status === 'needChange' && (
                  <textarea
                    className="memo-input"
                    placeholder="    "
                    value={item.memo}
                    onChange={(e) => updateReportItem(item.id, 'memo', e.target.value)}
                  />
                )}
              </div>
            ))}
          </div>
        </div>

        {/*    */}
        <div className="action-buttons">
          <div className="button-row">
            <button className="action-button cancel" onClick={handleBack}>
              
            </button>
            <button className="action-button save" onClick={handleSave}>
              
            </button>
          </div>
          <div className="button-row primary">
            <button 
              className="action-button primary"
              onClick={handleComplete}
            >
                   
            </button>
          </div>
        </div>
      </div>

      {/*    */}
      <ConfirmModal
        isOpen={showModal === 'unsaved'}
        onClose={() => setShowModal(null)}
        onConfirm={() => {
          setShowModal(null);
          onBack();
        }}
        title="   "
        message="  ?"
        confirmText=""
        cancelText=""
      />

      {/*     */}
      <ConfirmModal
        isOpen={showModal === 'inspectionFail'}
        onClose={() => setShowModal(null)}
        onConfirm={handleInspectionFailConfirm}
        title="  "
        message={
          <>
              ' ' .<br />
                .<br />
            ?
          </>
        }
        confirmText=""
        cancelText=""
      />

      {/*     */}
      <ConfirmModal
        isOpen={showModal === 'complete'}
        onClose={() => setShowModal(null)}
        onConfirm={handleCompleteConfirm}
        title="  "
        message={
          <>
               ?<br />
                ,<br />
               .
          </>
        }
        confirmText=" "
        cancelText=""
      />
    </div>
  );
};

export default MaintenanceReport;