import React, { useState } from 'react';

function BasicInfoTab({ onDataChange }) {
  const [formData, setFormData] = useState({
    businessName: '()',
    businessNumber: '123-45-67890',
    registrationNumber: '000-00-00000',
    companyPhone: '02-123-4567',
    address: '   123',
    detailAddress: ' 5',
    postalCode: '06234',
    email: 'partner@chaba.com',
    ceoName: '',
    ceoPhone: '010-1234-5678',
    ceoPhoneVerified: true, //    
    managerName: '',
    managerPhone: '010-9876-5432',
    bankName: '',
    accountNumber: '123-456-7890'
  });

  //   
  const [phoneVerification, setPhoneVerification] = useState({
    isVerifying: false,
    codeSent: false,
    verificationCode: '',
    timer: 0,
    originalPhone: '010-1234-5678' //   
  });

  const handleChange = (field, value) => {
    setFormData(prev => ({ ...prev, [field]: value }));
    
    //       
    if (field === 'ceoPhone' && value !== phoneVerification.originalPhone) {
      setFormData(prev => ({ ...prev, ceoPhoneVerified: false }));
    }
    
    onDataChange();
  };

  //  
  const handleSendVerificationCode = async () => {
    const phone = formData.ceoPhone.replace(/-/g, '');
    
    //   
    if (!/^010\d{8}$/.test(phone)) {
      alert('   .');
      return;
    }

    try {
      //   API 
      // const response = await fetch('/api/phone/send-code', {
      //   method: 'POST',
      //   headers: { 'Content-Type': 'application/json' },
      //   body: JSON.stringify({ phone: formData.ceoPhone })
      // });

      //  
      console.log(' :', formData.ceoPhone);
      
      setPhoneVerification(prev => ({
        ...prev,
        isVerifying: true,
        codeSent: true,
        timer: 180 // 3
      }));

      //  
      startTimer();
      
      alert(' .');
    } catch (error) {
      alert('  .');
    }
  };

  //  
  const startTimer = () => {
    const interval = setInterval(() => {
      setPhoneVerification(prev => {
        if (prev.timer <= 1) {
          clearInterval(interval);
          return { ...prev, timer: 0, codeSent: false };
        }
        return { ...prev, timer: prev.timer - 1 };
      });
    }, 1000);
  };

  //  
  const handleVerifyCode = async () => {
    if (!phoneVerification.verificationCode) {
      alert(' .');
      return;
    }

    try {
      //   API 
      // const response = await fetch('/api/phone/verify-code', {
      //   method: 'POST',
      //   headers: { 'Content-Type': 'application/json' },
      //   body: JSON.stringify({ 
      //     phone: formData.ceoPhone,
      //     code: phoneVerification.verificationCode 
      //   })
      // });

      //   (  )
      if (phoneVerification.verificationCode === '123456') {
        setFormData(prev => ({ ...prev, ceoPhoneVerified: true }));
        setPhoneVerification({
          isVerifying: false,
          codeSent: false,
          verificationCode: '',
          timer: 0,
          originalPhone: formData.ceoPhone
        });
        alert('  .');
        onDataChange();
      } else {
        alert('  .');
      }
    } catch (error) {
      alert('  .');
    }
  };

  //  
  const formatTimer = (seconds) => {
    const min = Math.floor(seconds / 60);
    const sec = seconds % 60;
    return `${min}:${sec.toString().padStart(2, '0')}`;
  };

  const handleAccountVerify = () => {
    alert('     .');
  };

  const handleAddressSearch = () => {
    new window.daum.Postcode({
      oncomplete: function(data) {
        const address = data.roadAddress || data.jibunAddress;
        
        setFormData(prev => ({
          ...prev,
          postalCode: data.zonecode,
          address: address,
          detailAddress: ''
        }));
        
        onDataChange();
        
        setTimeout(() => {
          const detailAddressInput = document.getElementById('detailAddress');
          if (detailAddressInput) {
            detailAddressInput.focus();
          }
        }, 100);
      }
    }).open();
  };

  return (
    <div className="basic-info-tab">
      <h3 className="section-title"> </h3>
      
      <div className="form-group">
        <label></label>
        <input
          type="text"
          value={formData.businessName}
          disabled
        />
      </div>

      <div className="form-group">
        <label></label>
        <input
          type="text"
          value={formData.businessNumber}
          disabled
        />
      </div>

      <div className="form-group">
        <label> </label>
        <input
          type="text"
          value={formData.registrationNumber}
          onChange={(e) => handleChange('registrationNumber', e.target.value)}
        />
      </div>

      <div className="form-group">
        <label> </label>
        <input
          type="tel"
          value={formData.companyPhone}
          onChange={(e) => handleChange('companyPhone', e.target.value)}
        />
      </div>

      <div className="form-group">
        <label></label>
        <div className="address-group">
          <div className="button-group" style={{ marginBottom: '8px' }}>
            <input
              type="text"
              value={formData.postalCode}
              placeholder=""
              readOnly
              style={{ width: '120px' }}
            />
            <button 
              className="btn-secondary" 
              onClick={handleAddressSearch}
              type="button"
            >
               
            </button>
          </div>
          <input
            type="text"
            value={formData.address}
            placeholder=" "
            readOnly
            onClick={handleAddressSearch}
            style={{ cursor: 'pointer', marginBottom: '8px' }}
          />
          <input
            id="detailAddress"
            type="text"
            value={formData.detailAddress}
            onChange={(e) => handleChange('detailAddress', e.target.value)}
            placeholder="  "
          />
        </div>
      </div>

      <div className="form-group">
        <label></label>
        <input
          type="email"
          value={formData.email}
          onChange={(e) => handleChange('email', e.target.value)}
        />
      </div>

      <hr className="section-divider" />

      <h3 className="section-title">   </h3>

      <div className="form-group">
        <label></label>
        <input
          type="text"
          value={formData.ceoName}
          onChange={(e) => handleChange('ceoName', e.target.value)}
        />
      </div>

      <div className="form-group">
        <label>
           
          {formData.ceoPhoneVerified && (
            <span className="verified-badge"> </span>
          )}
        </label>
        <div className="phone-verification-group">
          <div className="button-group" style={{ marginBottom: phoneVerification.codeSent ? '8px' : '0' }}>
            <input
              type="tel"
              value={formData.ceoPhone}
              onChange={(e) => handleChange('ceoPhone', e.target.value)}
              placeholder="010-0000-0000"
              disabled={phoneVerification.codeSent}
              style={{ flex: 1 }}
            />
            {!formData.ceoPhoneVerified && (
              <button 
                className="btn-secondary" 
                onClick={handleSendVerificationCode}
                disabled={phoneVerification.codeSent}
                type="button"
              >
                {phoneVerification.codeSent ? '' : ' '}
              </button>
            )}
          </div>
          
          {phoneVerification.codeSent && (
            <div className="verification-code-group">
              <div className="button-group">
                <input
                  type="text"
                  value={phoneVerification.verificationCode}
                  onChange={(e) => setPhoneVerification(prev => ({ 
                    ...prev, 
                    verificationCode: e.target.value 
                  }))}
                  placeholder=" 6"
                  maxLength="6"
                  style={{ flex: 1 }}
                />
                <span className="timer">{formatTimer(phoneVerification.timer)}</span>
                <button 
                  className="btn-secondary" 
                  onClick={handleVerifyCode}
                  type="button"
                >
                  
                </button>
              </div>
              <p className="help-text">
                 : 123456
              </p>
            </div>
          )}
        </div>
      </div>

      <div className="form-group">
        <label></label>
        <input
          type="text"
          value={formData.managerName}
          onChange={(e) => handleChange('managerName', e.target.value)}
        />
      </div>

      <div className="form-group">
        <label> </label>
        <input
          type="tel"
          value={formData.managerPhone}
          onChange={(e) => handleChange('managerPhone', e.target.value)}
        />
      </div>

      <hr className="section-divider" />

      <h3 className="section-title"> </h3>

      <div className="form-group">
        <label></label>
        <select
          value={formData.bankName}
          onChange={(e) => handleChange('bankName', e.target.value)}
        >
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
        </select>
      </div>

      <div className="form-group">
        <label></label>
        <div className="button-group">
          <input
            type="text"
            value={formData.accountNumber}
            onChange={(e) => handleChange('accountNumber', e.target.value)}
            style={{ flex: 1 }}
          />
          <button className="btn-secondary" onClick={handleAccountVerify}>
            
          </button>
        </div>
      </div>
    </div>
  );
}

export default BasicInfoTab;